<template>
  <div id="Tip">
      <div class="teaching">
            <p class="title">指令下注方法</p>
            <p>在聊天室中输入想要下注的项目后再加上数字，按送出键后便能下注。</p>
            <p>z,b：庄</p>
            <p>x,p：闲</p>
            <p>zd,bp：庄对</p>
            <p>xd,pp：闲对</p>
            <p>h,t：和</p>
            <p>sb：三宝</p>
            <p>输入C则可取消本局所有下注</p>
            <p>例如：zd100</p>
            <p>表示下注庄对100</p>
            <p class="title" ng-bind="lang.info"></p>
            <img src="../images/tutorial/1.jpg" />
            <p class="title" ng-bind="lang.chatlog"></p>
            <img src="../images/tutorial/2.jpg" />
            <p class="title" ng-bind="lang.chatlog"></p>
            <img src="../images/tutorial/3.jpg" />
            <p class="title" ng-bind="lang.chatlog"></p>
            <img src="../images/tutorial/4.jpg" />
            <p class="title" ng-bind="lang.historyChat">历史记录</p>
            <img src="../images/tutorial/5.jpg" />
            <p class="title" ng-bind="lang.record"></p>
            <img src="../images/tutorial/6.jpg" />
     </div>
  </div>
</template>
<style lang="less" scoped>
#Tip{
    display: block;
    background-color: #1f1f1e;
    .teaching {
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    background-color: #1f1f1e;
    }
    .teaching p {
    font-size: 1rem;
    color: #ffffff;
    padding: 5px 10vw;
    }
    .teaching p.title {
    font-size: 1.2rem;
    text-align: center;
    }
    .teaching img {
    width: 90vw;
    height: auto;
    border: 0;
    margin: 0 auto;
    display: block;
    }
}
</style>
<script>
const u = navigator.userAgent
const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
export default {
    data(){
        return {
            username:'',
            password:''
        }
    },
    mounted(){
        var vm = this
       
    },
    methods:{
        submit(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




