<template>
  <div id="GameRecode">
    <div v-if="showHistoruChat">
        <ChatBodyRecode :recode="recode" @back="showHistoruChat = false"></ChatBodyRecode>
    </div> 
    <div class="row" v-else>
        <table cellpadding="0" cellspacing="0" class="record" @click.stop>
            <thead>
                <tr>
                    <td  class="table-cell">台桌</td>
                    <td  class="table-cell">局 / 靴</td>
                    <td  class="table-cell">日期</td>
                </tr>
            </thead>
            <tbody class="tbody">
                <tr @click="getChatHistory(item.bacnum)" v-for="(item,index) in recordList" :key="index" class="ng-scope">
                    <td>
                        <span  class="">{{selectRoom}}</span>
                    </td>
                    <td>
                        <span ng-bind="item.shoeNo" class="">{{item.bacnum | initBacnum}}</span>
                    </td>
                    <td>
                        <span class="time">{{item.time}}</span>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>  
      
  </div>
</template>
<style lang="less" scoped>
#GameRecode{
    display: block;
    .row{
         display: flex;
        flex-direction: column;
        justify-content:center ;
        // align-items:center;
        // align-content:center;
        margin:  0 auto;
        width: 100%;
        .title {
            text-align: center;
            font-size: 1rem;
            color: #fff;
            position: relative;
            border-bottom: 1px solid #616161;
            line-height: 2.5rem;
            background: rgba(6, 6, 6, 0.8);
        }
        .close {
                position: absolute;
            right: 10px;
            width: 1.5rem;
            height: 1.5rem;
                top: 0.4rem;
                right: 0.4rem;
            text-decoration: none;
            color: #fff;
            background-image:url('../images/delete_icon.png');
            background-size:100% 100%;  
        }
        .record{
           width: 100%;
            overflow: hidden;
            margin: 0 auto;
            font-size: 0.8rem;
            border-color: grey;
            thead,tfoot {
                background-color: #444444;
                td {
                    color: #fff;
                    height: 2rem;
                    text-align: center;
                    border-right: 1px solid #333333;
                }
            }
        
            tbody{
                background-color: #767676;
                min-height: 2rem;
                td {
                    text-align: center;
                    color: #fff;
                    height: 2rem;
                    border-right: 1px solid #333333;
                    border-bottom: 1px solid #333333;
                }
            }
        }
    }
}
</style>
<script>
import ChatBodyRecode from '../chat/ChatBodyRecode.vue'
import { mapState } from 'vuex'
export default {
    data(){
        return {
            username:'',
            password:'',
            showHistoruChat:false,
            recode:[],//消息记录
        }
    },
    props:['recordList'],
    mounted(){
        var vm = this
    },
    computed:{
        ...mapState([
            'selectRoom',
        ])
    },
    components:{ChatBodyRecode},
    filters:{
        initBacnum(val){
            if(val){
               val =  val.replace(/ju/g, '局') 
               val =  val.replace(/xue/g, '靴') 
               return val
            }
            return ''
        }
    },
    methods:{
        getChatHistory(val){
          const me = this
          const href = 'myapi/v1/room/getchathistory/'
          me.$indicator.open();
          let formData = 'room_name='+me.selectRoom+'&bac_num='+val
          me.axios({
              url:href,
              method:'POST',
              data:formData
          }).then(({data})=>{
              console.log(data.msg instanceof Array)
              console.log(data.msg.length)
              me.recode = []
              if(data.msg){ 
                for(let i=0;i<data.msg.length;i++){
                    
                     
                    let item = data.msg[i]
                    item = item.replace(/'/g, '"') 
                    item = item.replace(/True/g, true)
                    item = item.replace(/"\[/g, '[')
                    item = item.replace(/]\"/g, ']')
                    item = item.replace(/\\"/g, '"')
                    // // if(item.substr(0,1)=='"'){
                    // // 	item = item.substr(1,item.length-1)
                    // // }
                    // // if(item.substr(item.length-1,1)=='"'){
                    // // 	item = item.substr(0,item.length-1)
                    // // }
                    try{
                        console.log(item)
                        var json = JSON.parse(item);
                        console.log(json)
                    } catch(e) {
                        console.log(e)
                        var json = eval("("+ item +")");
                    }
                    data.msg[i] = json
                 }
                 console.log(data.msg)
                 me.recode = data.msg
                 me.showHistoruChat = true
                //  let 
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




