import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'

Vue.use(Vuex)

const state = {
  roomList: [],//房间信息
  selectRoom:'',//选中房间
  selectVideo:'',//选中房间 的视频 路径
  userInfo:{},//用户信息
  msg_admin:0,//
  msg_type:0,//当msg_type : 3的时候需要前端做判断 
  ShowKeyboard:false,
  
  
  message_input:'',//用户输入的信息，
  message_bet:'',//用户投注信息
  bac_num:'',//台号 12xue3ju
  runing:false,//开始下zhu
  ResultContent:'',//下注结果
  gotResult:false,//是否弹窗显示中奖结果

  roadMapHistory:'',//历史xue数据
  tongji_ws:'',//统计
  logout:false,//是否清人
  cancalMessage:false,//是否 禁言

  money:0,//钱数
  reloadWS:0,//掉线重连 重连次数

  secureToken:'',//用户视频token
}

export default new Vuex.Store({
  state,
  actions,
  mutations: {
    ['merge'](state, payload) {
      Object.assign(state, payload);
    },
  },
})



