import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import store from '../store/index.js'
var rootIP = sessionStorage.getItem('ip')
let local = window.location.origin
if (local.indexOf('localhost') > 0) {
  axios.defaults.baseURL = 'http://'+rootIP
} else {
  axios.defaults.baseURL = local
}
// axios.defaults.baseURL = 'https://wx.shiyujia.com';
// axios.defaults.headers.common['Token'] = '111';
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// / 添加请求拦截器
axios.interceptors.request.use(function(config, res) {
    let token = window.sessionStorage.getItem('token') || ''
    let href = window.location.href
    let routerName = href.split('#/')[1]
    if(routerName.indexOf('?') != -1){
      routerName = routerName.split('?')[0]
    }
    config.headers.Authorization = `JWT ${token}`
    if(token == '' && routerName !='regist'){
        window.location.hash = '/'
    }
    // if (config.method === 'post') {
    //     config.data = res.stringify({
    //         ...config.data
    //     })
    // }
    return config
})

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    return response;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });



