function RoadMap() {
    return {
      big: function(data) {
        var sm = [];
        var number = 0;
        var result = [];
        for (var i = 0; i < data.length; i++) {
          var bpt = this.BPT(data[i]);
          if (bpt.result == "tie" && sm.length == 0) {
            bpt.number = bpt.number + 1;
            sm.push(bpt);
          } else if (bpt.result == "tie" && sm.length) {
            sm[sm.length - 1].number = sm[sm.length - 1].number + 1;
            sm[sm.length - 1].bp = bpt.bp ? bpt.bp : sm[sm.length - 1].bp;
            sm[sm.length - 1].pp = bpt.pp ? bpt.pp : sm[sm.length - 1].pp;
            sm[sm.length - 1].tie = 1;
          } else if (
            sm.length &&
            sm[sm.length - 1].result != bpt.result &&
            sm[sm.length - 1].result == "tie"
          ) {
            sm[sm.length - 1].result = bpt.result;
            sm[sm.length - 1].bp = bpt.bp ? bpt.bp : sm[sm.length - 1].bp;
            sm[sm.length - 1].pp = bpt.pp ? bpt.pp : sm[sm.length - 1].pp;
            // sm[sm.length - 1].number = sm[sm.length - 1].number + 1;
            sm[sm.length - 1].tie = 1;
          } else if (
            sm.length &&
            sm[sm.length - 1].result != bpt.result &&
            sm[sm.length - 1].result != "tie"
          ) {
            result.push(sm);
            sm = [];
            sm.push(bpt);
          } else if (sm.length && sm[sm.length - 1].result == bpt.result) {
            sm.push(bpt);
          } else {
            sm.push(bpt);
          }
        }
        if (sm.length) {
          result.push(sm);
        }
        return result;
      },
      param: {},
      createTable: function(rows, column) {
        var name = this.param.name;
        var scope = this.param.scope;
        var compile = this.param.compile;
        var element = this.param.element;
        var table = `<table class="` + name + `">`;
        for (var i = 0; i < rows; i++) {
          table = table + `<tr>`;
          for (var j = 0; j < column; j++) {
            table = table + `<td></td>`;
          }
          table = table + `</tr>`;
        }
        table = table + `</table>`;

        $(element).empty();
        $(element).append(table);
        var query = $("table." + name);
        this.param.table = query;
      },
      addColumn: function() {
        var name = this.param.name;
        var scope = this.param.scope;
        var table = this.param.table;
        var width = this.param.width;
        // var compile = this.param.compile;
        var ele = "";
        for (var i = 0; i < 6; i++) {
          ele = $(table.find("tr")[i]);
          ele.append("<td></td>");
        }
        var query = document.querySelector("table." + name);
        var table = $(query);
        table.css({
          width: "calc(" + table.find("tr")[0].children.length * width + "vw)"
        });
        this.param.table = table;
      },
      innerHTML: function(bb, spans, rows, column) {
        var img = this.param.img;
        var data = this.param.data;
        var table = this.param.table;
        var total = data.length + column;
        total = total > spans ? total : spans;
        if (table.find("tr")[0].children[total] == undefined) {
          this.addColumn();
        }
        var td = table.find("tr")[rows].children[spans];
        if (typeof bb == "object") {
          td.className = bb.result;
          // debugger
          td.innerHTML =
            img
              .replace(/{result}/, bb.result)
              .replace(/{bp}/, bb.bp)
              .replace(/{pp}/, bb.pp)
              .replace(/{tie}/, bb.tie) +
            (bb.number > 1 ? "<span>" + bb.number + "</span>" : "");
        } else {
          td.className = bb;
          td.innerHTML = img.replace(/{result}/, bb);
        }
      },
      for: function() {
        var table = this.param.table;
        var data = this.param.data;
        var column = 0;
        for (let spans in data) {
          spans = parseInt(spans);
          for (let rows in data[spans]) {
            rows = parseInt(rows);
            var bb = data[spans][rows];
            var id = bb;
            if (typeof bb == "object") {
              id = bb.result;
            }
            if (
              spans &&
              rows &&
              table.find("tr")[rows] &&
              !table.find("tr")[rows].children[spans + column].id &&
              table.find("tr")[rows].children[spans + column - 1].id &&
              table.find("tr")[rows].children[spans + column - 1].id == id
            ) {
              var offset = 0;
              for (var i = rows; i < data[spans].length; i++) {
                bb = data[spans][i];
                if (rows - 1 == 0) {
                  column++;
                  this.innerHTML(bb, spans + column, 0);
                } else {
                  offset++;
                  this.innerHTML(bb, spans + column + offset, rows - 1);
                }
              }
              break;
            } else if (
              spans &&
              rows &&
              table.find("tr")[rows] &&
              !table.find("tr")[rows].children[spans + column].id &&
              table.find("tr")[rows + 1] &&
              table.find("tr")[rows + 1].children[spans + column].id &&
              table.find("tr")[rows + 1].children[spans + column].id == id
            ) {
              var offset = 0;
              for (var i = rows; i < data[spans].length; i++) {
                bb = data[spans][i];
                if (rows - 1 == 0) {
                  column++;
                  this.innerHTML(bb, spans + column, 0);
                } else {
                  offset++;
                  this.innerHTML(bb, spans + column + offset, rows - 1);
                }
              }
              break;
            } else if (
              table.find("tr")[rows] &&
              table.find("tr")[rows].children[spans + column] &&
              table.find("tr")[rows].children[spans + column].id
            ) {
              var offset = 0;
              for (var i = rows; i < data[spans].length; i++) {
                bb = data[spans][i];
                if (rows - 1 == 0) {
                  column++;
                  this.innerHTML(bb, spans + column, 0);
                } else {
                  offset++;
                  this.innerHTML(bb, spans + column + offset, rows - 1);
                }
              }
              break;
            } else if (!table.find("tr")[rows]) {
              var offset = 0;
              for (var i = rows; i < data[spans].length; i++) {
                bb = data[spans][i];
                offset++;
                this.innerHTML(bb, spans + offset + column, 5);
              }
              break;
            } else {
              this.innerHTML(bb, spans + column, rows);
            }
          }
        }
      },
      // 大眼仔路单规则
      // 1、根据大路路单生成大眼仔路单。起始点从大路的第二行第二列开始。如果大路中该位置上没有出现“庄”或“闲”，则从大路的第三列第一行开始。
      // 2、当大路没有出现换列时:
      // a) 如果大路当前格的左边是空白，并且大路当前格的左边一格的上面一格不为空 (由有变无) 时,画一个蓝色的空心圆圈。
      // b) 如果不属于情况a时，画一个红色的空心圆圈。
      // 3、 当大路出现换列时:
      //  用大路没换列前的那两路进行比较,如果那两路的圆圈总数一样时，画一个红色的空心圆圈;否则画一个蓝色的空心圆圈

      // 小路的路单规则
      // 1、根据大路路单生成小路路单。起始点从大路的第三列第二行开始。如果大路中该位置上没有出现“庄”或“闲”，则从大路的第4列列第一行开始。
      // 2、 当大路没有出现换列时:
      // a) 如果大路当前格的左边第2列是空白，并且大路当前格的左边第2列的上面一格不为空 (由有变无) 时,画一个蓝色的实心圆圈。
      // b) 如果不属于情况a时，画一个红色的实心圆圈。
      // 3、 当大路出现换列时:
      // 以大路没换列前的第1路和第3路进行比较,如果那两路的圆圈总数一样时，画一个红色的实心圆圈;否则画一个蓝色的实心圆圈。

      //曱甴路单规则
      // 1、根据大路路单生成曱甴路单。起始点从大路的第四行第二列开始。如果大路中该位置上没有出现“庄”或“闲”，则从大路的第5列列第一行开始。
      // 2、 当大路没有出现换列时:
      // a) 如果大路当前格的左边第3列是空白，并且大路当前格的左边第3列的上面一格不为空 (由有变无) 时,画一个蓝色的由左下到右上的斜线。
      // b) 如果不属于情况a时，画一个红色的由左下到右上的斜线。
      // 3、 当大路出现换列时:
      // 以大路没换列前的第1路和第4路进行比较,如果那两路的圆圈总数一样时，画一个红色的由左下到右上的斜线;否则画一个蓝色的
      road: function(data) {
        var x = 0;
        var y = 0;
        if (data[this.param.x] && data[this.param.x][1]) {
          x = this.param.x;
          y = 1;
        } else if (data[this.param.y] && data[this.param.y][0]) {
          x = this.param.y;
          y = 0;
        }
        var column = false;
        var status = "";
        var sm = [];
        var result = [];
        for (var i = x; i < data.length; i++) {
          if (x == i && y > 0) {
            x = -1;
            column = false;
          } else {
            column = true;
          }
          for (var j = y; j < data[i].length; j++) {
            status = "";
            if (
              column == false &&
              data[i - this.param.x][j] == undefined &&
              data[i - this.param.x][j - 1] != undefined
            ) {
              status = "player";
            } else if (column == false) {
              status = "banker";
            } else if (
              column == true &&
              data[i - 1].length == data[i - this.param.y].length
            ) {
              status = "banker";
            } else {
              status = "player";
            }
            if (sm.length == 0) {
              sm.push(status);
            } else if (sm.length && sm[sm.length - 1] == status) {
              sm.push(status);
            } else if (sm.length && sm[sm.length - 1] != status) {
              result.push(sm);
              sm = [];
              sm.push(status);
            }
            column = false;
          }
          y = 0;
        }
        result.push(sm);
        return result;
      },

      /**
       * 2  庄赢
       * 18 庄赢庄对
       * 10 庄赢闲对
       * 26 庄赢，庄闲对
       * 1  闲赢
       * 17 闲赢，庄对
       * 9  闲赢，闲对
       * 25 闲赢，庄闲对
       * 4  和
       * 20 和，庄对
       * 12 和，闲对
       * 28 和，闲对
       * banker 庄
       * player 闲
       * 
       * **/
      BPT: function(param) {
        switch (param) {
          case "2":
            // 庄赢
            return {
              result: "banker",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "18":
            // 庄赢，庄对
            return {
              result: "banker",
              bp: 1,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "10":
            // 庄赢，闲对
            return {
              result: "banker",
              bp: 0,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "26":
            // 庄赢，庄闲对
            return {
              result: "banker",
              bp: 1,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "1":
            // 闲赢
            return {
              result: "player",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "17":
            // 闲赢，庄对
            return {
              result: "player",
              bp: 1,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "9":
            // 闲赢，闲对
            return {
              result: "player",
              bp: 0,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "25":
            // 闲赢，庄闲对
            return {
              result: "player",
              bp: 1,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "4":
            // 和
            return {
              result: "tie",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "20":
            // 和，庄对
            return {
              result: "tie",
              bp: 1,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "12":
            // 和，闲对
            return {
              result: "tie",
              bp: 0,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "28":
            // 和，闲对
            return {
              result: "tie",
              bp: 1,
              pp: 1,
              tie: 0,
              number: 0
            };
            break;
          case "banker":
            return {
              result: "banker",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          case "player":
            return {
              result: "player",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            break;
          default:
            return {
              result: "",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
        }
      }
    };
  }
  export default RoadMap;


