<template>
  <div class="body-wrapper-chat" ref="body_wrapper"  @scroll="onscroll" @click="hideBoard">
    <template v-for="msgObj in CHAT.msgArr">
      <!-- <system-msg :msg="msgObj.user.username" v-if="msgObj.login"></system-msg> -->
      <template v-if="!msgObj.login&&!msgObj.logout">
        <other-msg v-if="msgObj.from_username != userInfo.username" :msgObj="msgObj" :isNotRecord="true"></other-msg>
        <self-msg v-if="msgObj.from_username == userInfo.username" :msgObj="msgObj"></self-msg>
      </template>
    </template>
    <!-- <other-msg></other-msg> -->
    <a class="icon-todown" @click="pulldown()" v-if="showtodown"></a>
  </div>

</template>

<script>
import OtherMsg from './OtherMsg'
import SelfMsg from './SelfMsg'
import { mapState,mapMutations } from 'vuex'
import {CHAT} from '../../assets/client'
export default {
  data () {
    return {
      CHAT,
      showtodown:false,
      ismouseMove:false,
      t: {
          sx: 0,
          s: 0,
          y:0,//yzhou
          m: 0,
          e: 0
      },
      overflow:false,
    }
  },
  watch:{
    'CHAT.msgArr'(val){
      // this.overflow =true
      console.log('msgArr变化')
      if(!this.showtodown){
            this.pulldown()
      }
    }
  },
  mounted(){
  },
    computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'userInfo',
    ])
  },
  components:{
    OtherMsg,
    SelfMsg,
  },
  methods:{
    ...mapMutations([
      'merge'
    ]),
    hideBoard(){
        this.merge({
          ShowKeyboard:false
        })
    },
    onscroll(e){
      var scrollTop = e.target.scrollTop
      var scrollHeight = e.target.scrollHeight
      let swiperHeight =  e.target.clientHeight
      if (scrollHeight - scrollTop > swiperHeight + 250) {
        this.showtodown = true
      } else {
        this.showtodown = false
      }
    },
    pulldown(){
      var vm = this
      setTimeout(()=>{
          let height =vm.$refs.body_wrapper.scrollHeight 
           vm.$refs.body_wrapper.scrollTo(0,height)
           vm.overflow = false
      },0)

    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>
.mint-tab-container-wrap,.mint-tab-container-item{
    height: 100%;
}
.body-wrapper-chat{
  display: block;
  background-color: #eee;
  transition: all ease 1s;
  height: ~'calc(100vh - 21rem)';
  overflow-y: auto;
  color: #333;
  padding: 0 5px;
  transition: all ease 0.5;
  padding-bottom: 2rem;
  .icon-todown{
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
bottom: 9rem;
    right: 2rem;
    background-image:url('../images/down.png'); 
    background-size:100% 100%; 
  }
}

</style>


