<template>
  <div class="body-wrapper-user" ref="body_wrapper_user" @scroll="onscroll" @click="hideBoard">
    <span class="back" @click="$emit('back')">返回</span>
    <template v-for="msgObj in CHAT_USER.msgArr">
      <template v-if="!msgObj.login&&!msgObj.logout">
        <other-msg v-if="msgObj.from_username != userInfo.username" :msgObj="msgObj" :isNotRecord="false"></other-msg>
        <self-msg v-if="msgObj.from_username==userInfo.username" :msgObj="msgObj"></self-msg>
      </template>
    </template>
    <a class="icon-todown" @click="pulldown()" v-if="showtodown"></a>
  </div>

</template>

<script>
import OtherMsg from './OtherMsg'
import SelfMsg from './SelfMsg'
import { mapMutations ,mapState} from 'vuex'
import {CHAT_USER} from '../../assets/client'
export default {
  data () {
    return {
      showtodown:false,
      CHAT_USER
    }
  },
  mounted(){
    if(this.recode){
         CHAT_USER.init(this.recode)
    }else{

    }
  },
  computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'userInfo',
    ])
  },
  components:{
    OtherMsg,
    SelfMsg,
  },
  props:['recode'],
  methods:{
    ...mapMutations([
      'merge'
    ]),
    hideBoard(){
        this.merge({
          ShowKeyboard:false
        })
    },
    onscroll(e){
      var scrollTop = e.target.scrollTop
      var scrollHeight = e.target.scrollHeight
      let swiperHeight =  e.target.clientHeight
      if (scrollHeight - scrollTop > swiperHeight + 50) {
        this.showtodown = true
      } else {
        this.showtodown = false
      }
    },
    pulldown(){
      var vm = this
      console.log(vm.$refs.body_wrapper.scrollHeight)
      let height =vm.$refs.body_wrapper.scrollHeight
      vm.$refs.body_wrapper.scrollTo(0,height)
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' >
.mint-tab-container-wrap,.mint-tab-container-item{
    height: 100%;
}
.body-wrapper-user{
  background-color: #eee;
  height: ~'calc(100% - 105px)';
  overflow-y: scroll;
  color: #333;
  padding: 0 5px;
  transition: all ease 0.5;
  padding-bottom: 2rem;
  .icon-todown{
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
    bottom: 9rem;
    right: 2rem;
    background-image:url('../images/down.png'); 
    background-size:100% 100%; 
  }
  .back{
    display: block;
    position: absolute;
    top: 1rem;
    left: 1rem;
    // width: 3rem;
    height: 2rem;
    line-height: 2rem;
    text-align: right;
    padding-right: 10px;
    border-radius: 5px;
    padding-left: 2rem;
    z-index: 100;
    font-size: 1rem;
    color: #333;
    background-image:url('../images/back.png'); 
    background-size:auto 1.5rem; 
    background-repeat:no-repeat;
    background-position:5px;  
    background-color:#ffc107; 
  }
}
</style>


