<script>
import { mapState } from 'vuex'

export default {
    data(){
        return {
            result:this.content,
            gameResultData:{
                playerCard1:{},
                playerCard2:{},
                playerCard3:{},
                bankerCard1:{},
                bankerCard2:{},
                bankerCard3:{},
            },
            zhuangPai:'',
            order:'z',//上一句庄赢还是闲赢 ， 默认庄
        }
    },
    name:'NickName',
    mounted(){
        var vm = this
        console.log(vm.result)
        // vm.result = [
        //     "b4h4",
        //     "f2m3",
        //     "z,zd"
        // ]
        vm.initPoker()

    },
    filters:{
        fliter_hanzi(val){
            if(!val)return ''

            if(val.indexOf('ju') != -1){
                val = val.replace(/ju/, "局")
            }
             if(val.indexOf('xue') != -1){
                val = val.replace(/xue/, "靴")
            }
            return val
        }
    },
     computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
        'bac_num',
        'userInfo',
        ])
    },
    props:['content'],
    methods:{
        initPoker(){
               /**
         * msg_type: 4 本靴结果  结果在content里 是数组['h4m4', 'h3f2', 'z,zd']
         *  第一个是庄的牌 第二个是闲的牌 (b黑桃h红桃f方块m梅花) h4m4f3 就达标 红桃4梅花4方块3  
         *   z,zd  表示 庄赢 同时有庄对  如果只有z 就表示庄赢没有对
            添加了msg_type 4的 roomchat消息 这个就对应了

            ws://58.64.147.72:5120/webrtc_playnow/singleport/udp/8003_VISITOR_HD/sid:2174590f6059d3503c3b8cfe16a8222a
         * **/
            var vm = this
            if(!vm.result)return false
            let zhuang = vm.result[0]
            let xian = vm.result[1]
            let playerCard_1 = xian.substr(0,2)
            let playerCard_2 = xian.substr(2,2)
            //闲
            if(String(xian).length == 6){
                let playerCard_3 = xian.substr(4,2)
                vm.gameResultData.playerCard3 = vm.initCard(playerCard_3)
            }
            vm.gameResultData.playerCard1 = vm.initCard(playerCard_1)
            vm.gameResultData.playerCard2 = vm.initCard(playerCard_2)
            //庄
             let layerCard_1 = zhuang.substr(0,2)
            let layerCard_2 = zhuang.substr(2,2)
             if(String(zhuang).length == 6){
                let layerCard_3 = zhuang.substr(4,2)
                vm.gameResultData.bankerCard3 = vm.initCard(layerCard_3)
            }
            vm.gameResultData.bankerCard1 = vm.initCard(layerCard_1)
            vm.gameResultData.bankerCard2 = vm.initCard(layerCard_2)
        },
        initCard(val){
            //分别调用初始化 花色和值得方法
            var vm = this
            let color = val.substr(0,1)
            let num = val.substr(1,1)
            let obj = {
                color:vm.initColor(color),
                num:vm.initPokerNumber(num),
            }
            // let obj = {
            //     color:'3',
            //     num:10,
            // }
            return obj
        },
        initColor(val){
            /**
             * class 
             * p1-1 表示 A-方片
             * -1方片
             * -2梅花
             * -3黑桃
             * -4红桃
             * **/
            let color = ''
            switch(val){
                case 'b':
                color = '3'
                break;
                 case 'h':
                color = '4'
                break;
                 case 'f':
                color = '1'
                break;
                 case 'm':
                color = '2'
                break;
            }
            return color
        },
        initPokerNumber(val){
            val = String(val)
            switch(val){
                case '0':
                val = '10'
                 break;
                case 'j':
                val = '11'
                break;
                 case 'q':
                val = '12'
                break;
                 case 'k':
                val = '13'
                break;
            }
            return val
        },
        initZD(){
            var vm = this
            if(!vm.result){
                setTimeout(()=>{
                      vm.initZD()
                 },100)
                 return false
            }else{
                let msg = ''
                vm.order = vm.result[3]?vm.result[3]:'z'
                switch(vm.result[2]){
                case 'z':
                    msg =  '<p style="color:red;">庄赢</p>'
                    break;
                case 'z,zd':
                    msg =  '<p style="color:red;">庄赢</p><p style="color:red;">庄对</p>'
                    break;
                case 'z,xd':
                    msg =  '<p style="color:red;">庄赢</p><p style="color:red;">闲对</p>'
                    break;
                case 'z,sd':
                    msg =  '<p style="color:red;">庄赢</p><p style="color:red;">庄闲对</p>'
                    break;
                case 'x':
                    msg =  '<p style="color:#0000ff;">闲赢</p>'
                    break;
                case 'x,xd':
                    msg =  '<p style="color:#0000ff;">闲赢</p><p style="color:#0000ff;">闲对</p>'
                    break;
                case 'x,zd':
                    msg =  '<p style="color:#0000ff;">闲赢</p><p style="color:#0000ff;">庄对</p>'
                    break;
                case 'x,sd':
                    msg =  '<p style="color:#0000ff;">闲赢</p><p style="color:#0000ff;">庄闲对</p>'
                    break;
                case 'h':
                    msg =  '<p style="color:#1aa617">和局</p>'
                    break;
                case 'h,zd':
                    msg =  '<p  style="color:#1aa617">和局</p><p  style="color:#1aa617">庄对</p>'
                    break;
                case 'h,xd':
                    msg =  '<p  style="color:#1aa617">和局</p><p  style="color:#1aa617">闲对</p>'
                    break;
                case 'sb':
                    msg =  '<p  style="color:#1aa617">和 庄对 闲对</p>'
                    break;
                }
                return msg
            }
                 
        },
        chooseImg(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        selectPoker(){

        },
    }
}
</script>




