<template>
  <div class="showThisCloseaccount" @click="$emit('showHide')">
      <div class="row"  >
        <div class="title" @click.stop>
            <span  class="ng-binding">{{bac_num_thisxue | fliter_hanzi}} 结算表格</span>
        </div>
        <table cellpadding="0" cellspacing="0" class="record" @click.stop>
            <thead>
                <tr>
                    <td  class="table-cell">{{bac_num_thisxue | fliter_hanzi}}</td>
                    <td  class="table-cell">本局得分</td>
                    <td  class="table-cell">剩余分</td>
                </tr>
            </thead>
            <tbody class="tbody">
                <tr  v-for="(item,index) in list" :key="index" class="ng-scope">
                                        <!-- "content": [
"{\" name:david,\"bet_add\": 222, \"bet_reduce\": 233, \"result\": \"x\", \"usermoney\": 940989}",
"{\" name:david,\"bet_add\": 222, \"bet_reduce\": 233, \"result\": \"x\", \"usermoney\": 940989}",
] -->
                    <td>
                        <span  class="">{{item.username}}</span>
                    </td>
                    <td>
                        <span  class="">{{item.bet_add}}</span>
                    </td>
                    <td>
                        <span  class="">{{item.usermoney}}</span>
                    </td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                   <td>
                        <span  class="">总计:{{listLength?listLength:0}}</span>
                    </td>
                    <td colspan='2' >
                        <span class="">{{totalCount}}</span>
                    </td>
                </tr>
            </tfoot>
        </table>
    </div>
  </div>
</template>
<style lang="less" scoped>
.showThisCloseaccount{
    display: flex;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    .row{
         display: flex;
        flex-direction: column;
        justify-content:center ;
        // align-items:center;
        // align-content:center;
        margin:  0 auto;
        width: 95vw;
        .title {
            text-align: center;
            font-size: 1rem;
            color: #fff;
            position: relative;
            border-bottom: 1px solid #616161;
            line-height: 2.5rem;
            background: rgba(6, 6, 6, 0.8);
        }
        .close {
                position: absolute;
            right: 10px;
            width: 1.5rem;
            height: 1.5rem;
                top: 0.4rem;
                right: 0.4rem;
            text-decoration: none;
            color: #fff;
            background-image:url('../src/images/delete_icon.png');
            background-size:100% 100%;  
        }
        .record{
           width: 100%;
            overflow: hidden;
            margin: 0 auto;
            font-size: 0.8rem;
            margin-bottom: 50px;
            border-color: grey;
            thead,tfoot {
                background-color: #444444;
                td {
                    color: #fff;
                    height: 2rem;
                    text-align: center;
                    border-right: 1px solid #333333;
                }
            }
        
            tbody{
                background-color: #767676;
                min-height: 2rem;
                td {
                    text-align: center;
                    color: #fff;
                    height: 2rem;
                    border-right: 1px solid #333333;
                    border-bottom: 1px solid #333333;
                }
            }
        }
    }
}
</style>
<script>
import { mapState } from 'vuex'
export default {
    data(){
        return {
            list:this.betlist,
            totalCount:0,
        }
    },
    name:'showThisCloseaccount',
    props:['betlist','bac_num_thisxue'],
    mounted(){
        var vm = this
        console.log(vm.list)
        vm.list.forEach(element => {
            let num = Number(element.bet_add) - Number(element.bet_reduce)
            vm.$set(element,'total',num)
            vm.totalCount = vm.totalCount+num
        });
        setTimeout(()=>{
            vm.$emit('showHide')
        },10000)
    },
      filters:{
        fliter_hanzi(val){
            if(!val)return ''

            if(val.indexOf('ju') != -1){
                val = val.replace(/ju/, "局")
            }
             if(val.indexOf('xue') != -1){
                val = val.replace(/xue/, "靴")
            }
            return val
        }
    },
    computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
        'userInfo',
        ]),
        listLength(){
            if(this.list){
                return this.list.length
            }
            return 0
        }
    },
    methods:{
        chooseImg(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        selectPoker(){

        },
    }
}
</script>




