<template>
  <div class="header">
      <select name="" class="changeLang" v-model="selectRoom" @change="initRoom">
          <option :value="item.room_id" v-for="(item,index) in roomList" :key="index">{{item.room_name}}</option>
      </select>
      <marquee direction="left" scrolldelay='0' v-text="msg" class="marquee"></marquee>   
      <span v-text="info_game_start+countdown" :class="['headerTopTips',{'runing':runing}]"></span>
  </div>
</template>
<style lang="less" scoped>
.header{
    display: block;
    height: 2rem;
    width: 100vw;
    background: #505050;
    .changeLang {
        display: block;
        color: #fff;
        background: url(../src/images/triangle-down.png) no-repeat right #505050;
        background-size: 15px 10px;
        background-position-y: 0.75rem;
        background-position-x: 95%;
        font-size: 14px;
        border: 0;
        outline: 0;
        height: 2rem;
        padding: 0 20px 0 10px;
        margin: 0;
        -webkit-appearance: none;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
    }
    .marquee {
        display: inline-block;
        color: #ffffff;
        line-height: 2rem;
        height: 2rem;
        overflow: hidden;
        border: 0;
        background: #505050;
        position: absolute;
        top: 0;
       left: 0rem;
    right: 0rem;
        font-size: 0.875rem;
    }
    .runing{
        background: #0f9911!important;
    }
    .headerTopTips{
        display: block;
        color: #fff;
        line-height: 2rem;
        height: 2rem;
        font-size: 0.9rem;
        padding: 0 10px;
        position: absolute;
        z-index: 10;
        right: 0;
        top: 0;
        background: #ca9d39;
    }
}
</style>
<script>
import { mapMutations ,mapState} from 'vuex'
import {CHAT,ADMIN_CHAT,VIDEO_WS} from '../assets/client'
export default {
    data(){
        return {
          info_game_start:'停止下注',
          msg:'',
          roomList:[],
          selectRoom:'',
          countdown:'',
        }
    },
    mounted(){
        var vm = this
        vm.getRoomList()
        vm.getMsg()
    },
    watch:{
        runing(val){
            if(val){
                this.startGame()
            }else{
                this.stoptGame()
            }
        },
        reloadWS(val){
            if(val){ 
                this.getRoomList()
            }
        }
    },
    computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
          'runing',
          'userInfo',
          'reloadWS',
        ])
    },
    methods:{
        ...mapMutations([
            'merge'
        ]),
        getMsg(){
          const me = this
          const href = 'myapi/v1/Banner/bannermsg/'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'GET',
          }).then(({data})=>{
              console.log(data)
              //true 就是投注 false 就是平常状态
              if(data.code == 0){
                 me.msg = data.msg
              }else{
                me.$toast(data.detail)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        getGameStatus(){
          const me = this
          const href = 'myapi/v1/room/roomstatus/'
          me.$indicator.open();
          let FormData = 'room_name='+me.selectRoom
          me.axios({
              url:href,
              method:'POST',
              data:FormData
          }).then(({data})=>{
              console.log(data)
              //true 就是投注 false 就是平常状态
              if(data.code == 0){
                  if(data.msg == 'true'){//开始状态
                    me.merge({
                        bac_num:data.bac_num,
                        msg_type:3,
                        msg_admin:100
                    })
                  }else{
                    me.merge({
                        bac_num:data.bac_num,
                        msg_type:0,
                        msg_admin:0
                    })
                  }
              }else{
                me.$toast(data.detail)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        startGame(){
            var vm =this
            vm.countdown = 70
            vm.info_game_start = '开始下注'
            let timer = setInterval(()=>{
                vm.countdown = vm.countdown-1
                if( vm.countdown <= 1){
                    clearInterval(timer)
                }
            },1000)
        },
        stoptGame(){
            var vm =this
            vm.info_game_start = '停止下注'
            vm.countdown = ''
        },
        initRoom(){
            var me = this
            sessionStorage.setItem('selectRoom',me.selectRoom)
            let selectVideo = ''
            for(let i=0;i<me.roomList.length;i++){
                if(me.roomList[i].room_id == me.selectRoom){
                    selectVideo = me.roomList[i].room_video
                    break;
                }
            }
            me.merge({
                selectRoom:me.selectRoom,
                selectVideo:selectVideo
            })
            var chatlogout = CHAT.logout()
            var adminlogout = ADMIN_CHAT.logout()
            if(chatlogout && adminlogout){
                setTimeout(()=>{
                    me.stoptGame()
                    me.initWebSocket()
                },1000)
            }
        },
        initWebSocket(){
             console.log(this.userInfo)
             var vm = this
            let userInfo = sessionStorage.getItem('userInfo')
            if(!vm.userInfo.username && userInfo){
                userInfo = JSON.parse(userInfo)
                vm.merge({
                    userInfo:userInfo
                })
            }
            if(!vm.userInfo.username || !vm.selectRoom){
                setTimeout(()=>{
                    vm.initWebSocket()
                },100)
                return false
            }else{
                vm.getGameStatus()
                CHAT.init()
                ADMIN_CHAT.init()
                // VIDEO_WS.init()
            }
        
            console.log(CHAT,'ww')
        },
        getRoomList(){
          const me = this
          const href = 'myapi/v1/room/roomlist/'
          me.$indicator.open();
          let FormData = ''
          me.axios({
              url:href,
              method:'POST',
              data:FormData
          }).then(({data})=>{
              if(data.code == 0){
                me.roomList = data.msg
               
                if(me.roomList && me.roomList.length>0){
                    let selectRoom_session = sessionStorage.getItem('selectRoom')
                    let selectVideo = ''
                    if(selectRoom_session != null){
                          me.selectRoom = selectRoom_session
                          for(let i=0;i<me.roomList.length;i++){
                              if(me.roomList[i].room_id == selectRoom_session){
                                  selectVideo = me.roomList[i].room_video
                                  break;
                              }
                          }
                    }else{
                          me.selectRoom = me.roomList[0].room_id
                          selectVideo = me.roomList[0].room_video
                    }
                     me.merge({
                        roomList:me.roomList,
                        selectRoom:me.selectRoom,
                        selectVideo:selectVideo
                    })
                }else{
                    me.$toast('房间为空')
                }
                me.initWebSocket()
              }else{
                me.$toast(data.detail)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
              if(err.response.data.detail == 'yours token is not allow to access.' || err.response.status == 401){
                   me.$indicator.close();
                    me.merge({
                        logout:true
                    })
                   me.$router.push('/')
              }
          })
        },
    }
}
</script>




