import axios from 'axios'
var vm = this
export const getBannerList = ({commit}) => {
  axios.get('/find/index', {}).then((res) => {
    if (res.data.code === 1) {
      commit('setBannerList', res.data.data)
      commit('setBannerNum', res.data.data)
    } else {
      vm.$toast(res.data.msg)
    }
  })
}









