<script>
import Ludan from './roadmap.js'
import { mapState,mapMutations } from 'vuex';
export default {
    data(){
        return {
            username:'',
            password:'',
            result:"",
            tongji:[0,0,0,0,0],//统计
            total_tongji:0,
            showYueYou:true,
            showXiaoLu:true,
        }
    },
    mounted(){
      this.GetRoad()
    },
     computed:{
         ...mapState([
          'selectRoom',
          'roadMapHistory',
          'tongji_ws',
          'bac_num'
        ])
    },
     filters:{
        initBacnum(val){
            if(val){
            //    val =  val.replace(/ju/g, '局') 
            //    val =  val.replace(/xue/g, '靴') 
               return val.split('xue')[0]+'靴'
            }
            return ''
        }
    },
    watch:{
        roadMapHistory(val){
            if(val){
                this.result = val
                this.initAll()
            }
        },
        tongji_ws(val){
            if(val != ''){
                this.initTongji(val)
            }
        },
        selectRoom(val){
            if(val){
                this.GetRoad()
            }
        }
    },
    methods:{
        ...mapMutations([
            'merge'
        ]),
        GetRoad(){
            const me = this
            if(!me.selectRoom){
                setTimeout(()=>{
                    me.GetRoad()
                },200)
               return false
            }
            const href = 'myapi/v1/room/getroad/'
            me.$indicator.open();
            let FormData = 'room_name='+me.selectRoom
            me.axios({
                url:href,
                method:'POST',
                data:FormData
            }).then(({data})=>{
                console.log(data)
                if(data.code == 0){
                    // data.msg.result_list = "2,2,2,4,1,1,4,2,2,2,2,2,1,4,10,2,2,2,1,4,4,2,2,2,1,2,1,10,1,1,1,10,2,4,1,1,2,12,2,4,2,2,2"
                     me.result = data.msg.result_list
                     if(me.result.result_list != ''){
                        me.merge({
                            roadMapHistory:me.result
                        })
                        me.initTongji(data.msg.tongji)
                        me.initAll()
                     }
                }else{
                    me.$toast(data.msg)
                }
                me.$indicator.close();
            }).catch((err)=>{
            console.log(err)
            })
        },
        initTongji(val){
            if(val == '')return false
            var me = this
            me.tongji = val.split(',')
            let total = 0
            for(let i=0;i<me.tongji.length;i++){
                total = total + Number(me.tongji[i])
            }
            me.total_tongji = total
        },
        initAll(){
            var vm = this
            var road = new Ludan()
            console.log('初始化路珠')
            //eyeRoadBg  eyeRoadBlock
            $('.eye-road-panel').empty()
            vm.initbig(road)
            vm.initZhuLu(road)
            vm.initEyeRoadPanel(road)
            vm.initTip(road)
            vm.init_jiayoulu_map(road)//小路和youyulu
        },
        init_jiayoulu_map(road){
            var table = `<table class="smallRoachBg">`;
            for (var i = 0; i < 3; i++) {
            table = table + `<tr>`;
            for (var j = 0; j < 20; j++) {
                table = table + `<td></td>`;
            }
            table = table + `</tr>`;
            }
            table = table + `</table>`;
            table = table + `<div class="smallRoadBlock" small-road-panel>`;
            table = table + `</div>`;
            table = table + `<div class="roachRoadBlock" roach-road-panel>`;
            table = table + `</div>`;
            $('.small-roach-road-panel').empty();
            $('.small-roach-road-panel').append(table);
            this.init_roachRoadPanel(road)
            this.initXiaoLu(road)
        },
        initbig(beadRoad){
            let result= this.result
            var beadPanel = $('.big-road-panel');
            beadRoad.param.name = "bigRoad";
            beadRoad.param.width = 3;
            beadRoad.param.element = '.big-road-panel';
            beadRoad.createTable(6, 25);

            if (result == undefined || !result) {
              return false;
            }
            var bead = result.split(",");
            var big = beadRoad.big(bead);

            beadRoad.param.img =
              '<span class="icon_dalu dalu_{result}_{bp}_{pp}_{tie}"></span>';
            beadRoad.param.data = Object.assign(big,{});
            beadRoad.for();
            setTimeout(function() {
              beadPanel[0].scrollLeft = beadPanel[0].scrollWidth;
            }, 1000);
        },
        initZhuLu(beadRoad){
           let result= this.result
            var table = `<table class="beadRoad">`;
            for (var i = 0; i < 6; i++) {
              table = table + `<tr>`;
              for (var j = 0; j < 15; j++) {
                table = table + `<td></td>`;
              }
              table = table + `</tr>`;
            }
            table = table + `</table>`;
            beadRoad.param.element = '.bead-road-panel';
            $('.bead-road-panel').empty();
            $('.bead-road-panel').append(table);

            var table = $('table.beadRoad');
            
            if (result == undefined) {
              return false;
            }
            var bead = result.split(",");

            var x = 0;
            var y = 0;
            var id = 0;
            var img = '<span class="icon_zhupan zhupan_{name}""></span>';
            var compile = "";
            for (let key in bead) {
              let td = table.find("tr")[y].children[x];
              if (td == undefined) {
                for (let i = 0; i < 6; i++) {
                  $(table.find("tr")[i])
                    .append("<td></td>");
                }
                // selector = document.querySelector("table.beadRoad");
                table = $("table.beadRoad");

                table.css({
                  width:
                    "calc(" +
                    table.find("tr")[y].children.length * 5 +
                    "vw - 3px)"
                });
                td = table.find("tr")[y].children[x];
              }
              if (bead[key]) {
                id = bead[key];
                if (id == 2 || id == 18 || id == 10 || id == 26) {
                  compile = `<span>庄</span>`;
                } else if (id == 1 || id == 17 || id == 9 || id == 25) {
                  compile = `<span>闲</span>`;
                } else {
                  compile = `<span>和</span>`;
                }
                compile = img.replace(/{name}/, bead[key]) + compile;
                $(td).append(compile);
              }

              y++;
              if (y >= 6) {
                y = 0;
                x++;
              }
            }

            setTimeout(function() {
              var panel = document.querySelector("div.bead-road-panel");
              var beadPanel = $(panel);
              beadPanel[0].scrollLeft = beadPanel[0].scrollWidth;
            }, 500);
        },
        initEyeRoadPanel(beadRoad){
                let result= this.result
                var table = `<table class="eyeRoadBg">`;
                for (var i = 0; i < 3; i++) {
                table = table + `<tr>`;
                for (var j = 0; j < 20; j++) {
                    table = table + `<td></td>`;
                }
                table = table + `</tr>`;
                }
                table = table + `</table>`;
                table = table + `<div class="eyeRoadBlock">`;
                table = table + `</div>`;

                $('.eye-road-panel').append(table);

                beadRoad.param.x = 1;
                beadRoad.param.y = 2;
                beadRoad.param.name = "eyeRoad";
                // beadRoad.param.scope = scope;
                beadRoad.param.width = 2.5;
                // beadRoad.param.compile = $compile;
                beadRoad.param.element = $("div.eyeRoadBlock")
                beadRoad.createTable(6, 40);

                if (result == undefined || result.length <= 1) {
                return false;
                }

                var bead = result.split(",");
                var big = beadRoad.big(bead);
                var eye = beadRoad.road(big);
                beadRoad.param.img = 
                '<span class="icon_eyeRoad dayanzai_{result}""></span>';
                beadRoad.param.data = Object.assign(eye,{});
                beadRoad.for();

                setTimeout(function() {
                    var beadPanel = $("div.eyeRoadBlock");
                    beadPanel[0].scrollLeft = beadPanel[0].scrollWidth;
                }, 500);
        },
        initXiaoLu(beadRoad){
            //small-roach-road-panel
            let result= this.result
            beadRoad.param.x = 2;
            beadRoad.param.y = 3;
            beadRoad.param.name = "smallRoad";
            beadRoad.param.width = 2.5;
            beadRoad.param.element = $("div.smallRoadBlock");
            beadRoad.createTable(6, 20);

            if (result == undefined || !result) {
              return false;
            }
            var bead = result.split(",");
            var big = beadRoad.big(bead);
            if(big.length<=2){
                this.showXiaoLu = false
                return false
            }
            try {
                 var small = beadRoad.road(big);
            }catch(e){
                console.log(e)
                return false
            }
           
            beadRoad.param.img =
              '<span class="icon_xiaolu xiaolu_{result}""></span>';
            beadRoad.param.data = Object.assign(small,{});
            beadRoad.for();

            setTimeout(function() {
              var beadPanel = $("div.smallRoadBlock");
              beadPanel[0].scrollLeft = beadPanel[0].scrollWidth;
            }, 500);
        },
        init_roachRoadPanel(beadRoad){
             let result= this.result

            beadRoad.param.x = 3;
            beadRoad.param.y = 4;
            beadRoad.param.name = "roachRoad";
            beadRoad.param.width = 2.5;
            beadRoad.param.element = $("div.roachRoadBlock")
            beadRoad.createTable(6, 20);

            if (result == undefined || !result) {
              return false;
            }

            var bead = result.split(",");
            var big = beadRoad.big(bead);
            if(big.length<=3){
                this.showYueYou = false
                return false
            }
            var roach = beadRoad.road(big);
            beadRoad.param.img =
              '<span class="icon_xiaolu xiaoqiang_{result}""></span>';
            beadRoad.param.data = Object.assign(roach,{});
            beadRoad.for();
            setTimeout(function() {
              var beadPanel = $("div.roachRoadBlock");
              beadPanel[0].scrollLeft = beadPanel[0].scrollWidth;
            }, 500);
        },
        initTip(beadRoad){
            let result= this.result
            var table = $("table.tips");
            const banker_obj = {
              result: "banker",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            const player_obj = {
              result: "player",
              bp: 0,
              pp: 0,
              tie: 0,
              number: 0
            };
            var bead = result.split(",");
            var big = beadRoad.big(bead);
            if (big && big[1]) {
              var img = '<span class="icon_tip dayanzai_{result}""></span>';
              var data = Object.assign([],big);
              var rs = data[data.length - 1][data[data.length - 1].length - 1];
              var banker = JSON.parse(JSON.stringify(data));
              var player = JSON.parse(JSON.stringify(data));
              console.log(banker)
              if (rs.result == "banker") {
                banker[banker.length - 1].push(banker_obj);
                player.push([player_obj]);
              } else {
                console.log(banker)
                player[player.length - 1].push(player_obj);
                console.log(banker)
                banker.push([banker_obj]);
              }
              beadRoad.param.x = 1;
              beadRoad.param.y = 2;
              // 大眼问路,庄
              var res = beadRoad.road(banker);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[1].children[1];
              td.innerHTML = img.replace(/{result}/, tips);
              // 大眼问路,闲
              var res = beadRoad.road(player);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[2].children[1];
              td.innerHTML = img.replace(/{result}/, tips);
            } else {
              var td = table.find("tr")[1].children[1];
              td.innerHTML = "";
              var td = table.find("tr")[2].children[1];
              td.innerHTML = "";
            }
            if (big && big[2]) {
              var img = '<span class="icon_tip xiaolu_{result}"></span>';
              var data = Object.assign([],big);
              var rs = data[data.length - 1][data[data.length - 1].length - 1];
              var banker = JSON.parse(JSON.stringify(data));
              var player = JSON.parse(JSON.stringify(data));
              if (rs.result == "banker") {
                banker[banker.length - 1].push(banker_obj);
                player.push([player_obj]);
              } else {
                player[player.length - 1].push(player_obj);
                banker.push([banker_obj]);
              }
              beadRoad.param.x = 2;
              beadRoad.param.y = 3;
              // 大眼问路,庄
              var res = beadRoad.road(banker);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[1].children[2];
              td.innerHTML = img.replace(/{result}/, tips);
              // 大眼问路,闲
              var res = beadRoad.road(player);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[2].children[2];
              td.innerHTML = img.replace(/{result}/, tips);
            } else {
              var td = table.find("tr")[1].children[2];
              td.innerHTML = "";
              var td = table.find("tr")[2].children[2];
              td.innerHTML = "";
            }
            if (big && big[3]) {
              var img = '<span class="icon_tip xiaoqiang_{result}"></span>';
              var data = Object.assign([],big);
              var rs = data[data.length - 1][data[data.length - 1].length - 1];
              var banker = JSON.parse(JSON.stringify(data));
              var player = JSON.parse(JSON.stringify(data));
              if (rs.result == "banker") {
                banker[banker.length - 1].push(banker_obj);
                player.push([player_obj]);
              } else {
                player[player.length - 1].push(player_obj);
                banker.push([banker_obj]);
              }
              beadRoad.param.x = 3;
              beadRoad.param.y = 4;
              // 大眼问路,庄
              if(banker.length<=3){
                  return false
              }
              var res = beadRoad.road(banker);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[1].children[3];
              td.innerHTML = img.replace(/{result}/, tips);
              // 大眼问路,闲
              var res = beadRoad.road(player);
              var tips = res[res.length - 1][res[res.length - 1].length - 1];
              var td = table.find("tr")[2].children[3];
              td.innerHTML = img.replace(/{result}/, tips);
            } else {
              var td = table.find("tr")[1].children[3];
              td.innerHTML = "";
              var td = table.find("tr")[2].children[3];
              td.innerHTML = "";
            }
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




