<template>
<div class="self-wrap">
  <span class="-name">{{msgObj.from_nickname}}</span>
  <span class="-header">
      <img class="portrait" :src="portrait">
  </span>
  <!-- <span class="-name">超级厉害</span> -->
  <div class="-msg">{{msg.content}}</div>
</div>
</template>

<script>
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg:this.msgObj
    }
  },
   computed:{
    portrait(){
    //  <img :src="(msgObj.avatar_url !='http://avatal.lelewuxian.com' && msgObj.avatar_url != '')?require('../images/user_'+msgObj.avatar_url+'.jpg'):require('../images/portrait.jpg')" alt="">
          if(this.msgObj.avatar_url != 'http://avatal.lelewuxian.com' && this.msgObj.avatar_url != '' && this.msgObj.avatar_url !='http://lelewuxian.com'){
              if(this.msgObj.avatar_url == 9){
                    return require('../images/portrait.jpg')
              }
              return require('../images/user_'+this.msgObj.avatar_url+'.jpg')
          }
          return require('../images/user_0.jpg')
      }
  },
  mounted(){
    console.log(this.msgObj)
  	this.$el.scrollIntoView()
  },
  props:["msgObj"]
}
</script>
<style lang='less'>
.self-wrap{
  /*background-color: #666;*/
 padding: 25px 5px 10px 5px;// 25px 5px 10px 60px;
  position: relative;
  overflow: hidden;
  .-name{
    display: block;
    position: absolute;
    top: 0;
    right: 60/16rem;
    font-size: 14px;
    padding: 2px;
    margin: 0 10px;
    color: #999;
  }
  .-header{
    display: block;
    position: absolute;
    top: 15px;
  	left: ~'calc(100% - 60px)';
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height:50/16rem;
    width: 50/16rem;
    border-radius: none;
    font-weight: bolder;
    img{
      display: block;
      width: 100%;
      height: 100%;
      background-image: url('../images/portrait.jpg');
      background-size: 100% 100%;
    }
  }
 /* .-name{
    background-color: #fff;
    position: absolute;
    font-size: 14px;
    padding: 2px;
    margin: 0 10px;
  }*/
  .-msg{
    background-color: #a0e75b;
    padding: 12px;

    float: right;
    position: relative;
    left: -68/16rem;
    top: 0px;
    border-radius: 6px;
    max-width: ~'calc(100% - 100px)';
    font-size: 16px;
    line-height: 16px;
    &:before{
      content:'';
      display: inline-block;
      height: 0;
      width: 0;
      border-left: solid 10px #a0e75b;
      border-top: solid 6px transparent;
      border-bottom : solid 6px transparent;
      position: absolute;
      top:15/16rem;
      left: 100%;
    }
  }
}
</style>


