<template>
  <div id="TALK">
        <chat-body></chat-body>
        <chat-foot></chat-foot>
  </div>
</template>
<style lang="less" scoped>
#TALK{
    display: block;
    height: 100%;
    position: relative;
}
</style>
<script>
import ChatBody from '../chat/ChatBody'
import ChatFoot from '../chat/ChatFoot'
const u = navigator.userAgent
const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
export default {
    data(){
        return {
            username:'',
            password:''
        }
    },
     components: {
        ChatBody,
        ChatFoot,
    },
    mounted(){
        var vm = this
       
    },
    methods:{
        submit(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




