<template>
 <div :class="['keyboard',{'active_keyboard':active}]">
      <ul class="btp">
        <li>
          <div id="player"  @click="putBet('x')" :class="[{'active_btn':message_bet=='x'}]">闲</div>
        </li>
        <li>
          <div id="banker" @click="putBet('z')" :class="[{'active_btn':message_bet=='z'}]">庄</div>
        </li>
        <li>
          <div id="tie"  @click="putBet('h')" :class="[{'active_btn':message_bet=='h'}]">和</div>
        </li>
      </ul>
      <ul class="result">
        <li>
          <div id="pp"  @click="putBet('xd')" :class="[{'active_btn':message_bet=='xd'}]">闲对</div>
        </li>
        <li>
          <div id="bp"  @click="putBet('zd')" :class="[{'active_btn':message_bet=='zd'}]">庄对</div>
        </li>
        <li>
          <div id="sb"  @click="putBet('sb')" :class="[{'active_btn':message_bet=='sb'}]">三宝</div>
        </li>
        <li>
          <div id="cancel" @click="send(true)" >撤销投注</div>
        </li>
      </ul>
      <table>
        <tbody><tr>
          <td>
            <div id="1" @click="addMsg(1)">1</div>
          </td>
          <td>
            <div id="2" @click="addMsg(2)">2</div>
          </td>
          <td>
            <div id="3" @click="addMsg(3)">3</div>
          </td>
          <td>
            <div id="undo" @click="addMsg(-1)">0</div>
          </td>
        </tr>
        <tr>
          <td>
            <div id="4" @click="addMsg(4)">4</div>
          </td>
          <td>
            <div id="5" @click="addMsg(5)">5</div>
          </td>
          <td>
            <div id="6" @click="addMsg(6)">6</div>
          </td>
          <td rowspan="3">
            <div id="bet" @click="send()">投注</div>
          </td>
        </tr>
        <tr>
          <td>
            <div id="7" @click="addMsg(7)">7</div>
          </td>
          <td>
            <div id="8" @click="addMsg(8)">8</div>
          </td>
          <td>
            <div id="9" @click="addMsg(9)">9</div>
          </td>
        </tr>
        <tr>
          <td>
            <div class="dis"></div>
          </td>
          <td>
            <div id="0"  @click="addMsg(0)">0</div>
          </td>
          <td>
            <div class="dis"></div>
          </td>
        </tr>
      </tbody></table>
  </div>
</template>
<style lang="less" scoped>
.active_keyboard{
     transform: translateY(0px)!important;
}
.keyboard {
    background: #efeaea;
    overflow: hidden;
    height: 263px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(263px);
    .btp {
      margin: 10px 0 0 0;
      padding: 0;
      width: 100%;
      text-align: center;
      .active_btn{
        background: red;
        color: #fff!important;
      }
        li {
        padding: 0;
        border-radius: 5px;
        display: inline-block;
        width: 32%;
        &:nth-child(1) div {
            color: #2a77d4;
        }
        &:nth-child(2) div {
            color: #d61d52;
        }
        &:nth-child(3) div {
            color: #27ce75;
        }
        div#cancel {
          background: #ff0000;
          color: #efefef;
      }
        div {
            width: 100%;
            height: 100%;
            font-size: 90%;
            line-height: 38px;
            text-align: center;
            outline: none;
            border-radius: 8px;
            border: 1px #cccaca solid;
            background: #efeeee;
            font-weight: bold;
        }
      }
    }
    table td + td {
        border-left: 1px solid #aeb2b5;
    }
    .result {
        margin: 10px 0 0 0;
        padding: 0;
        width: 100%;
        text-align: center;
          .active_btn{
        background: red;
        color: #fff!important;
      }
        li {
            padding: 0;
            border-radius: 5px;
            display: inline-block;
            width: 23.7%;
            &:nth-child(1) div {
                color: #2a77d4;
            }
            &:nth-child(2) div {
                color: #d61d52;
            }
            &:nth-child(3) div {
                color: #27ce75;
            }
            div#cancel {
                background: #ff0000;
                color: #efefef;
            }
            div {
                width: 100%;
                height: 100%;
                font-size: 90%;
                line-height: 38px;
                text-align: center;
                outline: none;
                border-radius: 8px;
                border: 1px #cccaca solid;
                background: #efeeee;
                font-weight: bold;
            }
        }
    }
    table {
        width: 100%;
        table-layout: fixed;
        border: none;
        border-collapse: collapse;
        border-spacing: 0;
        margin-top: 10px;
        tr {
            border-top: 1px solid #aeb2b5;
           td {
                background: linear-gradient(to bottom, #efeaea 10%, #dcdcdc 100%);
                #bet{
                  line-height: 114px;
                }
                div {
                  width: 100%;
                  height: 100%;
                  font-size: 1rem;
                  line-height: 38px;
                  text-align: center;
                  outline: none;
                  border: 0;
                  background: linear-gradient(to bottom, #efeaea 10%, #dcdcdc 100%);
                  padding: 0;
                  position: relative;
                  overflow: hidden;
                  &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        top: 0;
                        left: 0;
                        pointer-events: none;
                        background-image: radial-gradient(circle, #666 10%, transparent 10.01%);
                        background-repeat: no-repeat;
                        background-position: 50%;
                        transform: scale(10, 10);
                        opacity: 0;
                        transition: transform 0.3s, opacity 0.5s;
                    }
                }
                div#undo {
                  text-indent: 100vw;
                  background-image: url(../src/images/backspace.png);
                  background-size: 25px 25px;
                  background-position: center;
                  background-repeat: no-repeat;
              }
               div#bet {
                  height: calc(38px * 3);
                  font-weight: bold;
              }
            }
        }
    }
}
</style>
<script>
import { mapState ,mapMutations } from 'vuex'
import {CHAT} from '../assets/client'
import  md5 from 'js-md5';
export default {
    data(){
        return {
        }
    },
    props:['active'],
    name:'keyboard',
    mounted(){
        var vm = this
    },
    computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'message_input',
      'message_bet',
      'msg_admin',
      'userInfo',
      'selectRoom',
      'bac_num',
      'msg_type'
    ])
  },
    methods:{
        ...mapMutations([
          'merge'
        ]),
        addMsg(val){
          var vm = this
          let postMsg = ''
          if(val != -1){
              postMsg = vm.message_input+val
          }else{
            let m = vm.message_input
            if(m != ''){
              postMsg = m.substring(0,m.length-1)
            }
          }
          // if(postMsg == '') return false
          vm.merge({
            message_input:postMsg
          })
        },
        send(iscancle=false){
          var vm = this
          if(vm.message_input == '' && !iscancle){
              vm.$toast('请输入信息！')
            return false
          }
          const timestamp = Date.parse(new Date()); 
          let time = md5(vm.userInfo.username+timestamp+'')
          let msg_type = 0
          /**
           * msg_type:
              msg_type: 0 普通文本消息
              msg_type: 1 普通图片消息
              msg_type: 2 用户投注消息
              msg_type: 5 系统消息 这个消息需要前端高亮显示 (高亮就是用亮色体现出来)
              msg_type: 6 showbacc需要弹窗显示的消息同时高亮
              msg_type: 7 jiesuan 需要弹窗显示的消息同时高亮
              "msg_admin": 100 is_start
              "msg_admin": 200 is_stop
              "msg_admin": 300 is_show
              "msg_admin": 400 is_jiesuan 
              msg_admin 为100的时候  web端的消息构造要将msg_type改成2 就是后续web再发出的消息构造都是2为基础, 为投注消息.
              当 msg_admin 为200 的时候 需要将 msg_type 改成0 后续所有消息发送的构造都以0 或1 为基础!
              300和400 由服务端判断进行重铸消息体
              * **/
          if(vm.msg_admin == 100 && vm.msg_type == 3){
            msg_type =2
          }
          let msg = vm.message_bet+vm.message_input
          /**判断是否是撤销**/
          let content = ''
          if(iscancle){
              content = 'c'
          }else{
               content = msg
          }
          let formData = {
            msg_type: msg_type,
            msg_id: time,
            from_username: vm.userInfo.username,
            from_nickname:vm.userInfo.nickname,
            to_nickname:"",
            is_gruop: 'true',
            room_name: vm.selectRoom,
            avatar_url: vm.userInfo.avatar_url,
            image_url: "",
            msg_admin: '',
            bac_num:vm.bac_num,
            content:content
          }
          // console.log(formData)
          CHAT.submit(formData)
          //清空投注数据
          vm.merge({
            message_input:'',
            message_bet:'',
          })
        },
        putBet(val){
            this.merge({
                message_bet:val
            })
        },
        addNum(val){
            var vm =this
            //  *注: username 只让用英文+数字
            //  密码8位以上
            if(!vm.setPassowrdAgainStatus){//第一次输入
                if(vm.firstPass.length>=8){
                    
                }
                vm.firstPass.push(val)
            }else{

            }     
        },
        submit(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
    }
}
</script>




