<template>
<div>
  <div id="Container" :class="[{'Box-active':ShowKeyboard}]">
      <headers></headers>
      <div id="myVideo" ref="myVideo">   
           <video style="background-color:black" class="video" id="remoteVideo" width="800" height="450" autoplay playsinline controls muted></video>
      </div>
    <ul class="selectTop clearfix">
        <li @click="switchTab(index)" :class="['item-select',{'item-active':active=='tab-'+index}]" v-for="(item,index) in tabList" :key="index">{{item}}</li>
    </ul>
    
      <div id="Box"  :class="['Box_'+active]">
            <mt-tab-container v-model="active" :swipeable='false'>
                <mt-tab-container-item id="tab-0">
                    <ludan></ludan>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-1">
                   <talk></talk>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-2">
                    <User-info @refresh="getUserInfo()"></User-info>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-3">
                   <Game-recode :recordList="recordList"></Game-recode>
                </mt-tab-container-item>
                <mt-tab-container-item id="tab-4">
                    <Tip></Tip>
                </mt-tab-container-item>
            </mt-tab-container>
      </div>
  </div>
  <keyboard :active="ShowKeyboard" v-if="ShowKeyboard"></keyboard> 
  <showResult v-if="gotResult" v-on:showHide="show_Result()" :content="ResultContent" ></showResult>
</div>
</template>
<style lang="less" scoped>
.Box-active{
    /* top: ~'calc(15rem - 263px)'!important; */
    transform: translateY(-255px)!important;
}
#Container{
    display: block;
    #myVideo{
        display: block;
        width: 100vw;
        height: 12.625rem;
        margin: 0;
        padding: 0;
        .video{
            display: block;
            width: 100%;
            height: 12.625rem;
            margin: 0;
            padding: 0;
        }
    }

     .selectTop{
            display: block;
            height: 2.5rem;
            width: 100vw;
            background-color: #474a4f;
            z-index: 100;
            .item-select{
                float: left;
                width: 20%;
                text-align: center;
                color: #fff;
                line-height: 2.5rem;
                height: 100%;
                font-size: 0.875rem;
            }
            .item-active{
                border-bottom: 2px solid #d89b4f;
               color: #d89b4f;
            }
        }
    #Box{
        display: block;
        width: 100vw;
        height: ~'calc(100vh - 17.5rem)';
        // overflow: hidden;
        position: relative;
        .mint-tab-container,.mint-tab-container-wrap,.mint-tab-container-item{
            height: ~'calc(100vh - 17.5rem)';
            box-sizing: border-box;
        }
        .mint-tab-container-item{
                overflow-y: auto;
        }
       
    }
    .Box_tab-2{
        background: #1f1f1e;
    }
}
</style>
<script>
import showResult from '../components/showResult.vue'
import headers from '../components/header.vue'

import {CHAT,ADMIN_CHAT} from '../assets/client'
import keyboard from '../components/keyboard.vue'
import ludan from './content/Ludan.vue'
import UserInfo from './content/UserInfo.vue'//用户信息
import Tip from './content/Tip.vue'//用户投注记录
import GameRecode from './content/GameRecode.vue'//用户投注记录
import talk from './content/talk.vue'
import pako from 'pako'
import {mapState,mapMutations} from 'vuex'

const u = navigator.userAgent
const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端

import UnrealWebSocketPlayer from './video/UnrealWebSocketPlayer.js'
import './video/adapter-latest.js'
import UnrealWebRTCPlayer from './video/unrealwebrtcplayer.js'
export default {
    data(){
        return {
            username:'',
            password:'',
            active:'tab-0',
            tabList:[
                '路单','聊天下注','记录','历史记录','教学'
            ],
            recordList:[],//历史投注信息
        }
    },
    components:{headers,ludan,UserInfo,talk,GameRecode,keyboard,Tip,showResult},
    computed:{
        ...mapState([
            'ShowKeyboard',
            'msg_admin',
            'logout',
            'userInfo',
            'selectRoom',
            'secureToken',
            'selectVideo',
            'ResultContent',
            'gotResult'
        ]),
    },
    watch:{
        logout(val){
            if(val){
                this.log_out()
            }
        },
        active(val){
            if(val == 'tab-2'){
                this.getUserInfo()
            }else  if(val == 'tab-3'){
                this.getRecord()
            }
        },
        secureToken(val){
            if(val){
                this.initVideo()
            }
        },
        selectVideo(val){
             if(val){
                this.initVideo()
            }
        }
    },
    mounted(){
        var vm = this
        vm.initVideo()
    },
    methods:{
        ...mapMutations([
        'merge'
        ]),
        show_Result(){
            
          var vm = this
          let gotResult = !vm.gotResult
          vm.merge({
            gotResult:gotResult,//显示本xue结果
          })
        },
        initVideo(){
            var vm =this
            if(vm.secureToken == '' || vm.selectVideo == ''){
                return false
            }
            if(isIOS){
                var webrtcPlayer = null;
                //"8002_HD"
                console.log(vm.selectVideo)
                webrtcPlayer = new UnrealWebRTCPlayer("remoteVideo",vm.selectVideo,vm.secureToken,"58.64.147.72","5120","false",!0,"udp");
                webrtcPlayer.Play(); 
            }else{
                var webrtcPlayer = null;
                webrtcPlayer =  new UnrealWebSocketPlayer("remoteVideo", 800,
                600,"58.64.147.72", "5120","false",
                vm.selectVideo,vm.secureToken, !0, !1, 1, "",!1)
            }

        },
        getRecord(){
          const me = this
          if(!me.selectRoom){
              setTimeout(()=>{
                  me.getRecord()
              },100)
              return false
          }
          const href = 'myapi/v1/room/getchatlist/'
          me.$indicator.open();
          let formData = 'room_name='+me.selectRoom
          me.axios({
              url:href,
              method:'POST',
              data:formData
          }).then(({data})=>{
              console.log(data)
              if(data.msg && data.code == 0){
                  me.recordList = data.msg.reverse()
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        getUserInfo(){
            const me = this
            if(!me.userInfo.username)return false
            const href = 'myapi/v1/user/userinfo/'
            me.$indicator.open();
            let formData = 'username='+me.userInfo.username
            me.axios({
                url:href,
                method:'POST',
                data:formData
            }).then(({data})=>{
                console.log(data)
                if(data.code == 0){
                    this.merge({
                        userInfo:data.msg,
                        money:data.money
                    })
                }
                me.$indicator.close();
            }).catch((err)=>{
                console.log(err)
            })
        },
        log_out(){
            var me =this
            const href = 'myapi/v1/user/logout/'
            me.$indicator.open();
            me.axios({
                url:href,
                method:'POST',
            }).then(({data})=>{
                console.log(data)
                if(data.ret == 'ok'){
                    CHAT.logout()
                    ADMIN_CHAT.logout()
                    window.sessionStorage.removeItem('token')
                    window.sessionStorage.removeItem('userInfo')
                }
                me.$indicator.close();
            }).catch((err)=>{
                console.log(err)
            })
            me.$router.push('/login')
        },
        switchTab(index){
            this.active = 'tab-'+index
        },
        submit(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        regist(){
            this.$router.push('/regist')
        }
    }
}
</script>




