import Vue from 'vue'
import Router from 'vue-router'
import login from '@/src/Login.vue'
import Regist from '@/src/Regist.vue'
import index from '@/src/Container.vue'
import Ludan from '@/src/content/Ludan.vue'//ludan


Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/',
      name: 'loding',
      redirect:'login',
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/regist',
      name: 'Regist',
      component: Regist
    },
    {
      path: '/index',
      name:'main',
      component: index,
    }
  ]
})
router.beforeEach((to, from, next) => {
  next()
})
export default router


