<template>
  <div class="changeNickName" >
      <div class="row"  @click="$emit('showHide')">
            <!-- <a class="return" href="javascript:void(0);" @click.self="$emit('showHide')"></a> -->
            <div class="cell">
                <div class="title"  @click.stop>
                    <span  class="ng-binding">设置昵称</span>
                    <a class="close" @click.self.stop="$emit('showHide')"></a>
                </div>
                <div class="con"  @click.stop>
                    <form novalidate="false" class="edit-name ng-pristine ng-valid">
                        <input type="text" v-model="name" class="ng-pristine ng-untouched ng-valid ng-empty">
                        <button type="submit" class="determine ng-binding" @click="submit">确定</button>
                    </form>
                </div>
            </div>
        </div>
  </div>
</template>
<style lang="less" scoped>
.changeNickName{
    width: 100%;
    height: 100%;
    display: table;
    color: #ffffff;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
        background: rgba(0,0,0,.6);
     .row {
    display: table-row;
        .cell {
            display: table-cell;
            vertical-align: middle;
            .title {
                width: 98%;
                max-width: 300px;
                text-align: center;
                font-size: 1rem;
                color: #fff;
                position: relative;
                border-bottom: 1px solid #616161;
                line-height: 2.5rem;
                background: #323332;
                margin: 0 auto;
            }
            .close {
                 position: absolute;
                right: 10px;
                width: 1.5rem;
                height: 1.5rem;
                   top: 0.4rem;
                  right: 0.4rem;
                text-decoration: none;
                color: #fff;
                background-image:url('../src/images/delete_icon.png');
                background-size:100% 100%;  
            }
            .con {
                margin: 0 auto;
                text-align: center;
                width: 98%;
                max-width: 300px;
                background: #323332;
                input {
                    border: 0;
                    padding: 0;
                    color: #333;
                    border-radius: 3px;
                    margin: 15px 0;
                    font-size: 1rem;
                    width: 80%;
                    height: 30px;
                    outline: none;
                }
                .determine {
                    background-color: #aebc86;
                    border: 0;
                    margin: 0;
                    padding: 0;
                    color: #333;
                    border-radius: 3px;
                    margin-bottom: 15px;
                    font-size: 1rem;
                    width: 80%;
                    height: 30px;
                    outline: none;
                }
            }
        }
     }
}
</style>
<script>
import { mapState,mapMutations } from 'vuex';
export default {
    data(){
        return {
            name:'',
        }
    },
    name:'NickName',
    mounted(){
        var vm = this
    },
    computed:{
         ...mapState([
          'userInfo'
        ])
    },
    methods:{
         ...mapMutations([
            'merge'
        ]),
        submit(){
           const me = this
            if(me.name == ''){
               me.$toast('请输入昵称')
               return false
            }
            const href = 'myapi/v1/user/modifyuser/'
            me.$indicator.open();
            let FormData = 'username='+me.userInfo.username+'&nickname='+me.name
            me.axios({
                url:href,
                method:'POST',
                data:FormData
            }).then(({data})=>{
                console.log(data)
                if(data.code == 0){
                    var obj = me.userInfo
                    obj.nickname = me.name
                    me.merge({
                         userInfo:obj
                    })
                    me.$emit('showHide')
                }else{
                    me.$toast(data.msg)
                }
                me.$indicator.close();
            }).catch((err)=>{
            console.log(err)
            })
        },
    }
}
</script>




