<template>
  <div>
      <div v-if="msgObj.msg_type == 8">
          <system-msg  :msg="msgObj.content"></system-msg>
      </div>
       <div v-if="msgObj.msg_type == 1">
          <img :src="msgObj.image_url" class="img" alt="">
      </div>
      <div class="other-wrap" v-else>
        <span class="-header">
            <img class="portrait" :src="portrait">
        </span>
        <span class="-name">{{msgObj.from_username}}</span>
          <div :class="['other_msg',{'isAdmin':msgObj.msg_type == 5}]" v-if="msgObj.msg_type == 5">{{msgObj.content}}</div>
          <div :class="['other_msg',{'isAdmin_result':msgObj.msg_type == 6}]" v-else-if="msgObj.msg_type == 6" @click="show_bet(msgObj.content)">本局投注表</div>
          <div :class="['other_msg',{'isAdmin_result':msgObj.msg_type == 7}]" v-else-if="msgObj.msg_type == 7" @click="show_Closeaccount(msgObj.content)">本局玩家资料</div>
          <div :class="['other_msg','admin_msg','starting']" v-else-if="msgObj.msg_type == 3 && msgObj.msg_admin == 100">
            <img src="../images/start70.gif" alt="" class="img">
            <span class="number-countdown">{{CountdownNumber}}</span>
          </div>
          <div :class="['other_msg','detial-result']" v-else-if="msgObj.msg_type == 4" v-html="initZD()" @click="show_Result()"></div>
          <div :class="['other_msg']" v-else>{{msgObj.content}}</div>
        <!-- msg_type: 6 showbacc需要弹窗显示的消息同时高亮
          msg_type: 7 jiesuan 需要弹窗显示的消息同时高亮 -->
          

            <!-- <showResult v-if="gotResult" v-on:showHide="show_Result()" :content="initResult(msgObj.content)" ></showResult> -->
            <show-this-bet v-if="showBet" v-on:showHide="showBet = false" :betlist="initDate(msgObj.content)" ></show-this-bet>
            <show-this-closeaccount v-if="showCloseaccount" v-on:showHide="showCloseaccount  = false" :betlist="initDate(msgObj.content)" :bac_num_thisxue="msgObj.bac_num"></show-this-closeaccount>
      </div>
      <transition name="fade">
        <div v-show="showToast" :class="[{'toast-active':ShowKeyboard},'system-toast']" @click="showToast = false">
            <div class="bg"></div>
            <div class="row" @click.stop>
                <p class="title">提示</p>
                <div class="content" v-html="msgObj.content"></div>
                <p class="title"  @click="showToast = false">好</p>
            </div>
        </div>
    </transition>
  </div>
</template>

<script>
import { mapState ,mapMutations } from 'vuex'
import SystemMsg from './SystemMsg'
import showResult from '../../components/showResult.vue'
import showThisBet from '../../components/showThisBet.vue'
import showThisCloseaccount from '../../components/showThisCloseaccount.vue'
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg:this.msgObj,
      showBet:false,//展示 本局 投注表
      showCloseaccount:false,//展示 本局 投注表
      CountdownNumber:70,
      showToast:false,
    }
  },
  components:{showResult,showThisBet,showThisCloseaccount,SystemMsg},

  computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'message_input',
      'message_bet',
      'bac_num',
      'userInfo',
      'gotResult',
      'ShowKeyboard'
    ]),
    portrait(){
          if(this.msgObj.avatar_url != 'http://avatal.lelewuxian.com' && this.msgObj.avatar_url != '' && this.msgObj.avatar_url !='http://lelewuxian.com'){
              if(this.msgObj.avatar_url == 9){
                    return require('../images/portrait.jpg')
              }
              return require('../images/user_'+this.msgObj.avatar_url+'.jpg')
          }
          return require('../images/user_0.jpg')
      }
  },
  mounted(){
    console.log(this.msgObj)
    var vm =  this
    if(vm.msgObj.msg_type == 5 && vm.msgObj.to_nickname == vm.userInfo.nickname  && vm.isNotRecord){
        vm.showToast = true
        setTimeout(()=>{
          if(vm.showToast)vm.showToast = false
        },3000)
    }
    if(vm.msgObj.msg_type == 4 && vm.isNotRecord){
        let result = vm.initResult()
        vm.merge({
          ShowKeyboard:false,
          ResultContent:result,
          gotResult:true,//显示本xue结果
        })
    }
    console.log(vm.msgObj.msg_type+'::'+vm.msgObj.msg_admin)
    if(vm.msgObj.msg_type == 3 && vm.msgObj.msg_admin == 100){
      console.log('jinru')
        vm.initNum()
    }
  },
  props:{
    msgObj:{
      type:Object,
      default:{}
    },
    isNotRecord:{
      type:Boolean,
      default:false
    }
  },
  methods:{
      ...mapMutations([
        'merge'
      ]),
      initNum(){
        var vm = this
        // console
        vm.CountdownNumber = 70
         let timer = setInterval(()=>{
          //  console.log(vm.CountdownNumber)
              vm.CountdownNumber = vm.CountdownNumber-1
              if( vm.CountdownNumber <= 0){
                  clearInterval(timer)
              }
          },1000)
      },
      initResult(){
        if(this.msgObj.content instanceof Array){
            return this.msgObj.content
        }
          if(this.msgObj.content){
            console.log(this.msgObj.content)
            let result = JSON.parse(this.msgObj.content)
            return result.result
          }
          return {}
      },
      initZD(){
        let val = this.initResult()
        console.log(val)
        if(!val || !val[2])return ''
        let result = val[2]
        let msg = ''
        switch(result){
          case 'z':
            msg =  '<h1  style="color:red;">庄赢</h1>'
            break;
          case 'z,zd':
            msg =  '<h1  style="color:red;">庄赢</h1><h1  style="color:red;">庄对</h1>'
            break;
          case 'z,xd':
            msg =  '<h1  style="color:red;">庄赢</h1><h1  style="color:red;">闲对</h1>'
            break;
          case 'z,sd':
            msg =  '<h1  style="color:red;">庄赢</h1><h1  style="color:red;">庄闲对</h1>'
            break;
          case 'x':
            msg =  '<h1 style="color:#0000ff;">闲赢</h1>'
            break;
          case 'x,xd':
            msg =  '<h1 style="color:#0000ff;">闲赢</h1><h1 style="color:#0000ff;">闲对</h1>'
            break;
          case 'x,zd':
            msg =  '<h1 style="color:#0000ff;">闲赢</h1><h1 style="color:#0000ff;">庄对</h1>'
            break;
          case 'x,sd':
            msg =  '<h1 style="color:#0000ff;">闲赢</h1><h1 style="color:#0000ff;">庄闲对</h1>'
            break;
          case 'h':
            msg =  '<h1  style="color:#1aa617">和局</h1>'
            break;
          case 'h,zd':
            msg =  '<h1  style="color:#1aa617">和局</h1><h1  style="color:#1aa617">庄对</h1>'
            break;
          case 'h,xd':
            msg =  '<h1  style="color:#1aa617">和局</h1><h1  style="color:#1aa617">闲对</h1>'
            break;
          case 'sb':
            msg =  '<h1  style="color:#1aa617">和 庄对 闲对</h1>'
            break;
        }
        return msg
      },
      initDate(val){
        console.log(typeof(val))
        if(typeof(val) ==  "string"){
          val = val.replace(/'/g, '"') 
          let result = JSON.parse(val)
          console.log(result)
          return result
        }else{
          return val
        }
      
      },
      show_Result(){
          var vm = this
          let gotResult = !vm.gotResult
          let result = vm.initResult()
          vm.merge({
            ShowKeyboard:false,
            ResultContent:result,
            gotResult:gotResult,//显示本xue结果
          })
      },
      show_bet(){
         this.showBet = !this.showBet
      },
      show_Closeaccount(){
          this.showCloseaccount = !this.showCloseaccount
      },
      hide_table(){
          this.merge({
              msg_admin:0
          })
      },
  }
}
</script>
<style lang='less'>
.fade-enter-active, .fade-leave-active {
transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
opacity: 0;
}
.system-toast{
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content:center;
    align-items: center;
    .bg{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.4);
    }
    .row{
        display: block;
         position: absolute;
        // top: ~'calc(50% - 10rem)';
        // left:0;
        width: 80%;
        height: auto;
        border-radius: 10px;
        background: transparent;
        overflow: hidden;
        .title{
            display: block;
            font-size: 1rem;
            line-height: 2.4rem;
            text-align: center;
            color: #ffffff;
            background: #474a4f;
            &:last-of-type{
                line-height: 3rem;
                color: #2196f3;
            }
        }
        .content{
            background: #fff;
            padding: 1rem;
            font-size: 0.8rem;
            line-height: 1rem;
            color: #333;
        }
    }
}
.toast-active{
    .row{
         transform: translateY(200px)!important;
    }
}

.other-wrap{
  /*background-color: #666;*/
  padding: 25px 5px 10px 60/16rem;
  position: relative;
  .-header{
    display: block;
    position: absolute;
top: 15/16rem;
    left: 10/16rem;
    background-color: #905;
    height:50/16rem;
    width: 50/16rem;
    border-radius: 50%;
    font-weight: bolder;
     img{
      display: block;
      width: 100%;
      height: 100%;
    }
  
  }
  .-name{
    display: block;
    position: absolute;
    top: 0;
    left: 60/16rem;
    font-size: 14px;
    padding: 2px;
    margin: 0 10px;
    color: #999;
  }
  .other_msg{
    display: inline-flex;
    background-color: #fff;
   padding: 12px;
    position: relative;
    left: 10/16rem;
    top: 0px;
    border-radius: 6px;
    width: auto;
    max-width: ~'calc(100% - 100px)';
    font-size: 16px;
    line-height: 16px;
    .img{
      display: block;
      width: 120/16rem;
      height: 120/16rem;
    }
    .number-countdown{
      display: block;
      position: absolute;
      top: 5.9rem;
    left: 2.55rem;
      width: 60/16rem;
      height:28/16rem;
      border-radius: 15px;
      background: #26210a;
      font-size:22px;
      text-align: center;
      line-height: 28/16rem;
      color: #feed69;
    }
    &:before{
      content:'';
      display: inline-block;
      height: 0;
      width: 0;
      border-right: solid 10px #fff;
      border-top: solid 6px transparent;
      border-bottom : solid 6px transparent;
      position: absolute;
      top:5px;
      left: -8px;
    }
 
  }
  .admin_msg{

  }
  .detial-result{
        width: 10rem;
        // height: 5rem;
       top: 10px;
        background: #333;
        h1{
          display: block;
          width: 100%;
          text-align: center;
          color: red;
           font-size: 22px;
          line-height: 1.8rem;
          text-align: center;
          &:last-of-type{
            color: #ffffff;
          }
        }
        &:before{
          content:'';
          display: inline-block;
          height: 0;
          width: 0;
          border-right: solid 10px #333;
          border-top: solid 6px transparent;
          border-bottom : solid 6px transparent;
          position: absolute;
          top:5px;
          left: -8px;
        }
  }
  .isAdmin{
    background: orange;
      &:before{
      content:'';
      border-right: solid 10px orange;
    }
  }
  .isAdmin_result{
        background: linear-gradient(#e1cea3, #ca9d39);
        color: #515151;
        padding-left:1.8rem; 
 
        &:before{
        content:'';
        border-right: solid 10px #e1cea3;
      }
      &:after{
        content: '';
        display: block;
        width: 1.3rem;
        height: 1.3rem;
        display: block;
        position: absolute;
        top: 0.5rem;
        left: 0.5rem;
        background-image:url('../images/list.png');
        background-size:100% ;
      }
  }
}
</style>


