<template>
  <div class="showThisBet" @click="$emit('showHide')">
      <div class="row"  >
        <div class="title" @click.stop>
            <span  class="ng-binding">本局投注表</span>
        </div>
        <table cellpadding="0" cellspacing="0" class="record" @click.stop>
            <thead>
                <tr>
                    <td  class="table-cell">{{bac_num | fliter_hanzi}}</td>
                    <td  class="table-cell">庄</td>
                    <td  class="table-cell">闲</td>
                    <td  class="table-cell">和</td>
                    <td  class="table-cell">庄对</td>
                    <td  class="table-cell">闲对</td>
                </tr>
            </thead>
            <tbody class="tbody">
                <tr @click="getChatHistory(item)" v-for="(item,index) in list" :key="index" class="ng-scope">

                    <td>
                        <span  class="" v-text="item.username"></span>
                    </td>
                    <td>
                        <span  class="" v-text="item.z?item.z:0"></span>
                    </td>
                    <td>
                        <span  class="" v-text="item.x?item.x:0"></span>
                    </td>
                    <td>
                        <span  class="" v-text="item.h?item.h:0"></span>
                    </td>
                    <td>
                        <span  class="" v-text="item.zd?item.zd:0"></span>
                    </td>
                   <td>
                        <span  class="" v-text="item.xd?item.xd:0"></span>
                    </td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                   <td>
                        <span  class="">总计</span>
                    </td>
                    <td>
                        <span class="">{{z_total}}</span>
                    </td>
                       <td>
                        <span class="">{{x_total}}</span>
                    </td>
                       <td>
                        <span class="">{{h_total}}</span>
                    </td>
                       <td>
                        <span class="">{{zd_total}}</span>
                    </td>
                       <td>
                        <span class="">{{xd_total}}</span>
                    </td>
                </tr>
            </tfoot>
        </table>
    </div>
  </div>
</template>
<style lang="less" scoped>
.showThisBet{
    display: flex;
    background: rgba(0, 0, 0, 0.8);
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    .row{
         display: flex;
        flex-direction: column;
        justify-content:center ;
        // align-items:center;
        // align-content:center;
        margin:  0 auto;
        width: 95vw;
        max-height: calc(100vh * 0.5);
        .title {
            text-align: center;
            font-size: 1rem;
            color: #fff;
            position: relative;
            border-bottom: 1px solid #616161;
            line-height: 2.5rem;
            background: rgba(6, 6, 6, 0.8);
        }
        .close {
                position: absolute;
            right: 10px;
            width: 1.5rem;
            height: 1.5rem;
                top: 0.4rem;
                right: 0.4rem;
            text-decoration: none;
            color: #fff;
            background-image:url('../src/images/delete_icon.png');
            background-size:100% 100%;  
        }
        .record{
           width: 100%;
            overflow: hidden;
            margin: 0 auto;
            font-size: 0.8rem;
            margin-bottom: 50px;
            border-color: grey;
            thead,tfoot{
                background-color: #767676;
                td {
                    color: #fff;
                    height: 2rem;
                    text-align: center;
                    border-right: 1px solid #333333;
                }
            }
        
            tbody{
                background-color: #444444;
                min-height: 2rem;
                td {
                    text-align: center;
                    color: #fff;
                    height: 2rem;
                    border-right: 1px solid #333333;
                    border-bottom: 1px solid #333333;
                }
            }
        }
    }
}
</style>
<script>
import { mapState } from 'vuex'
export default {
    data(){
        return {
            list:this.betlist,
            z_total:0,
            x_total:0,
            h_total:0,
            zd_total:0,
            xd_total:0,
        }
    },
    name:'showThisBet',
    props:['betlist'],
    mounted(){
        var vm = this
        console.log(this.list)
        vm.list.forEach(element => {
              if(element.userbacc){

                  let arr = element.userbacc.split(',')
                  arr.forEach(item => {
                      let num = Number(item.replace(/[^0-9]+/ig,""))
                      let  zd = item.replace(/[^a-z]+/ig,"");
                      if(num){
                            switch(zd){
                                case 'z':
                                    vm.$set(element,'z',num)
                                    vm.z_total = vm.z_total+num
                                break;
                                 case 'x':
                                    vm.$set(element,'x',num)
                                     vm.x_total = vm.x_total+num
                                break;
                                 case 'h':
                                    vm.$set(element,'h',num)
                                    vm.h_total = vm.h_total+num
                                break;
                                 case 'xd':
                                    vm.$set(element,'xd',num)
                                    vm.xd_total = vm.xd_total+num
                                break;
                                 case 'zd':
                                    vm.$set(element,'zd',num)
                                    vm.zd_total = vm.zd_total+num
                                break;
                                 case 'sb':
                                    if(element.zd){
                                         vm.$set(element,'zd',element.zd+num)
                                    }else{
                                         vm.$set(element,'zd',num)
                                    }
                                    if(element.xd){
                                         vm.$set(element,'xd',element.xd+num)
                                    }else{
                                         vm.$set(element,'xd',num)
                                    }
                                     if(element.h){
                                         vm.$set(element,'h',element.h+num)
                                    }else{
                                        vm.$set(element,'h',num)
                                    }
                                   
                                   
                                    vm.zd_total = Number(vm.zd_total)+num
                                    vm.xd_total = Number(vm.xd_total)+num
                                    vm.h_total =  Number(vm.h_total)+num
                                break;
                            }
                      }
                 });

              }  
        });

        setTimeout(()=>{
            vm.$emit('showHide')
        },10000)
    },
    computed:{
        // 使用对象展开运算符将此对象混入到外部对象中
        ...mapState([
        'bac_num',
        'userInfo',
        ])
    },
    filters:{
        fliter_hanzi(val){
            if(!val)return ''

            if(val.indexOf('ju') != -1){
                val = val.replace(/ju/, "局")
            }
             if(val.indexOf('xue') != -1){
                val = val.replace(/xue/, "靴")
            }
            return val
        }
    },
    methods:{
        chooseImg(){
          const me = this
          const href = '/index.php?g=API&m=HelpAwarding&a=isNewMember'
          me.$indicator.open();
          me.axios({
              url:href,
              method:'POST',
              data:{
                  username:me.username,
                  password:me.password
              }
          }).then(({data})=>{
            /**
             * 判断是否是新用户 	
                0=非新用户 1=新用户
             * **/
              if(data.data.status == 0){
                  sessionStorage.setItem('is_old_user',0)
                  setTimeout(()=>{
                      me.$router.push('/activityHomePage')
                  },1000)
              }else if(data.data.status == 1){
                  sessionStorage.setItem('is_old_user',1)
                    setTimeout(()=>{
                         me.$router.push('/helperHomePage')
                    },1000)
              }else{
                me.$toast(data.message)
              }
              me.$indicator.close();
          }).catch((err)=>{
              console.log(err)
          })
        },
        selectPoker(){

        },
    }
}
</script>




