import Vue from 'vue'
function addZero(val) {
    if (val < 10) {
        return '0' + val
    } else {
        return val
    }
  }
  function getTimeStamp(time=0, flat=false)//注：time 指定天数， flat 指定0点
  {
     let  today = new Date();
      if (flat)
      {
          today.setHours(0);
          today.setMinutes(0);
          today.setSeconds(0);
          today.setMilliseconds(0);
      }
      let timeStamp = Date.parse(today) / 1000 + parseInt(time) * 60 * 60 * 24;
      return timeStamp;
  }
  /* eslint-disable no-new */
  Vue.filter("formatTime", function(value, type) {
    if (value) {
        var dataTime = ""
        var data = new Date()
        data.setTime(value)
        var year = data.getFullYear()
        var month = addZero(data.getMonth() + 1)
        var day = addZero(data.getDate())
        var hour = addZero(data.getHours())
        var minute = addZero(data.getMinutes())
        var second = addZero(data.getSeconds())
        if (type === 'YMD') {
            dataTime = year + '-' + month + '-' + day
        } else if (type === 'YMDHMS') {
          let today = getTimeStamp(0,true)//注：time 指定天数， flat 指定0点
          let D_value =(value/1000-today)
          if(D_value<86400){
            dataTime = '今日 ' + hour + ':' + minute
          }else if(D_value<172800){
            dataTime = '昨日 ' + hour + ':' + minute
          }else{
            dataTime = year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second
          }
        } else if (type === 'HMS') {
            dataTime = hour + ':' + minute + ':' + second
        } else if (type === 'YM') {
            dataTime = year + "-" + month
        }
    } else {
        dataTime = '无'
    }
    return dataTime // 将格式化后的字符串输出到前端显示
  })


