<template>
  <div class="userLogo" >
      <div class="row"  @click="$emit('showHide')">
            <!-- <a class="return" href="javascript:void(0);" @click.self="$emit('showHide')"></a> -->
            <div class="cell" >
                <div class="title"  @click.stop>
                    <span  class="ng-binding">修改图像</span>
                    <a class="close" @click.self.stop="$emit('showHide')"></a>
                </div>
               <div class="con"  @click.stop>
                <ul>
                    <li v-for="(item,index) in imgPool" :key="index" class="ng-scope">
                        <img :src="require('../src/images/user_'+item+'.jpg')" name="" @click="chooseImg(item)">
                    </li>
                  </ul>
               </div>
            </div>
        </div>
  </div>
</template>
<style lang="less" scoped>
.userLogo{
    width: 100%;
    height: 100%;
    display: table;
    color: #ffffff;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
        background: rgba(0,0,0,.6);
     .row {
    display: table-row;
        .cell {
            display: table-cell;
            vertical-align: middle;
            .title {
                width: 98%;
                max-width: 300px;
                text-align: center;
                font-size: 1rem;
                color: #fff;
                position: relative;
                border-bottom: 1px solid #616161;
                line-height: 2.5rem;
                background: #323332;
                margin: 0 auto;
            }
            .close {
                position: absolute;
                right: 10px;
                width: 1.5rem;
                height: 1.5rem;
                   top: 0.4rem;
                  right: 0.4rem;
                text-decoration: none;
                color: #fff;
                background-image:url('../src/images/delete_icon.png');
                background-size:100% 100%;  
            }
            .con {
                margin: 0 auto;
                text-align: center;
                width: 98%;
                max-width: 300px;
                background: #323332;
                max-height: 80vh;
                overflow: auto;
                ul {
                    width: 100%;
                    overflow: hidden;
                    list-style: none;
                    margin: 0;
                    padding: 0;
                    display: flex;
                    flex-wrap: wrap;
                     li {
                        list-style: none;
                        margin: 0;
                        padding: 0;
                        width: 20%;
                        img {
                            width: 90%;
                            height: auto;
                            display: block;
                            margin: 5% auto;
                        }
                    }
                }
            }
        }
     }
}
</style>
<script>
import { mapState,mapMutations } from 'vuex';
export default {
    data(){
        return {
            imgPool:[0,1,2,3,4]
        }
    },
    name:'NickName',
    mounted(){
        var vm = this
    },
     computed:{
         ...mapState([
          'userInfo'
        ])
    },
    methods:{
        ...mapMutations([
            'merge'
        ]),
        chooseImg(key){
            const me = this
            const href = 'myapi/v1/user/modifyuser/'
            me.$indicator.open();
            let FormData = 'username='+me.userInfo.username+'&avatar_url='+key
            me.axios({
                url:href,
                method:'POST',
                data:FormData
            }).then(({data})=>{
                console.log(data)
                if(data.code == 0){
                    var obj = me.userInfo
                    obj.avatar_url = key
                    me.merge({
                         userInfo:obj
                    })
                    me.$emit('showHide')
                }else{
                    me.$toast(data.msg)
                }
                me.$indicator.close();
            }).catch((err)=>{
            console.log(err)
            })
        },
    }
}
</script>




