<template>
  <div class="foot-wrapper">
    <span class="button-bet" @click="ShowKeyb"></span>
    <input class="chat-input" type="text" name="" @keyup.enter="send" v-model="msg" @change="postMsg" @focus="hideKeyBoard()">
    <span class="chat-sub" :class="{'primary':!!msg}"  @click.self="send">发送</span>
  </div>
</template>

<script>
import {CHAT} from '../../assets/client'
import store from '../../store/index.js'
import { mapState ,mapMutations } from 'vuex'
import  md5 from 'js-md5';
export default {
  data () {
    return {
      msg:''
    }
  },
  mounted(){
   
  },
  computed:{
    // 使用对象展开运算符将此对象混入到外部对象中
    ...mapState([
      'message_input',
      'message_bet',
      'bac_num',
      'msg_type',
      'msg_admin',
      'ShowKeyboard',
      'selectRoom',
      'userInfo',
      'cancalMessage'
    ])
  },
  watch:{
    message_input(val){
      this.msg = val
    },
    ShowKeyboard(val){
      if(!val){//隐藏
        this.merge({
          message_bet:''
        })
      }
    }
  },
  methods:{
    ...mapMutations([
      'merge'
    ]),
    postMsg(){
      var vm = this
      vm.merge({
        message_input:vm.msg,//用户输入信息
      })
    },
    hideKeyBoard(){
      var vm = this
      let ShowKeyboard = false
      vm.scrollBottom()
      vm.merge({
        ShowKeyboard:ShowKeyboard
      })
    },
    scrollBottom(){
        let timer = setInterval(function () {
            var sScrollTop = document.body.scrollTop || document.documentElement.scrollTop;
            var iSpeed = Math.floor((document.body.scrollHeight - sScrollTop) / 12);
            document.body.scrollTop = document.documentElement.scrollTop = sScrollTop + iSpeed;
            document.body.onmousewheel = function(){
                return false;
            };
            if (sScrollTop >= (document.body.scrollHeight/2-10)) {
                clearInterval(timer);
                document.body.onmousewheel = function(){
                    return true;
                };
            }
        }, 1);
    },
    ShowKeyb(){
      var vm = this
      let ShowKeyboard = !vm.ShowKeyboard
      vm.merge({
        ShowKeyboard:ShowKeyboard
      })
    },
    send(){
      var vm = this
       if(vm.cancalMessage){
        vm.$toast('你已经被禁言，请联系管理员')
        return false
      }
      if(vm.message_input == ''){
        vm.$toast('请输入信息！')
        return false
      }
      const timestamp = Date.parse(new Date()); 
      let time = md5(vm.userInfo.username+timestamp+'')
      let msg_type = 0
      /**
       * msg_type:
    msg_type: 0 普通文本消息
    msg_type: 1 普通图片消息
    msg_type: 2 用户投注消息
    msg_type: 5 系统消息 这个消息需要前端高亮显示 (高亮就是用亮色体现出来)
    msg_type: 6 showbacc需要弹窗显示的消息同时高亮
    msg_type: 7 jiesuan 需要弹窗显示的消息同时高亮
    "msg_admin": 100 is_start
    "msg_admin": 200 is_stop
    "msg_admin": 300 is_show
    "msg_admin": 400 is_jiesuan 
    msg_admin 为100的时候  web端的消息构造要将msg_type改成2 就是后续web再发出的消息构造都是2为基础, 为投注消息.
当 msg_admin 为200 的时候 需要将 msg_type 改成0 后续所有消息发送的构造都以0 或1 为基础!
300和400 由服务端判断进行重铸消息体
       * **/
      if(vm.msg_type == 3){
        if(vm.msg_admin == 100){
          msg_type =2
        }else if(vm.msg_admin == 200){
          msg_type =0
        }
      }
     
      // msg_type =2
      let msg = vm.message_bet+vm.message_input
      let formData = {
        msg_type: msg_type,
        msg_id: time,
        from_username: vm.userInfo.username,
        from_nickname:vm.userInfo.nickname,
        to_nickname:"",
        is_gruop: 'true',
        room_name: vm.selectRoom,
        avatar_url: vm.userInfo.avatar_url,
        image_url: "",
        msg_admin: '',
        bac_num:vm.bac_num,
        content:msg
      }
      //  console.log(formData)
      CHAT.submit(formData)
      vm.msg = ''
      vm.merge({
        message_input:''
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less' scoped>
.foot-wrapper{
  background-color: #ece7e7;
  height: 65px;
  flex-shrink: 0;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px 10px 12px;
  justify-content: space-between;
  border-top: solid 1px rgba(0,0,0,0.1);
  .button-bet{
    width: 2rem;
    height: 2rem;
    margin-top: .2rem;
    background-image:url('../images/keyboard.png'); 
    background-size:100% 100%;
    margin-right: .5rem; 
  }
  .chat-input{
    height: 42px;
    width: ~'calc(75% - 2.5rem)';
    border-radius: 5px;
    font-size: 18/16rem;
    outline: none;
    border:none; 
    box-sizing: border-box;
    padding: 5px;
  }
  .chat-sub{
    display: flex;
    background-color: rgba(0,0,0,0.2);
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 18%;
        border-radius: 5px;
    outline: none;
    color: #666;
  }
  .primary{
    background-color: #007afc;
    color: #fff;
  }
}
</style>


